import React, {
  forwardRef, useState, useEffect,
} from 'react';
import PropTypes from 'prop-types';
import { useI18n } from 'services/i18n';
import styled from 'styled-components';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';
import { styledSystem } from 'services/theme/helpers';
import { isBoolean } from 'lodash';

const Container = styled(Box)`
    display: flex;
    flex-direction: column;
    padding: 0 15px;
    margin-bottom: 14px;
    ${styledSystem};
`;

const StyledContainer = styled(Box)`
position: relative;
width: 50px;
padding: 2px;
background-color: ${({ theme, state }) => {
    if (isBoolean(state)) {
      return state
        ? theme.colors.toggleSwitchTrueBackground
        : theme.colors.toggleSwitchFalseBackground;
    }
    return theme.colors.toggleSwitchDisabledBackground;
  }};
border-radius: 16px;
`;

const StyledCircle = styled(Box)`
position: relative;
left: ${({ state }) => {
    if (isBoolean(state)) return state ? '66%' : '0';
    return '33%';
  }};
height: 16px;
width: 16px;
background-color: #FFFFFF;
border-radius: 50%;
`;

const Label = styled(Text)`
    margin-bottom: 7px;
`;

const ToggleSwitchDouble = forwardRef((props, ref) => {
  const { state, label, onChange } = props;
  const [currentState, setCurrentState] = useState();
  const t = useI18n();

  useEffect(() => {
    setCurrentState(state);
  }, [state]);

  const handleOnClick = () => {
    let newState = true;
    if (isBoolean(currentState)) {
      newState = currentState ? false : undefined;
    }
    setCurrentState(newState);
    onChange?.(newState);
  };

  return (
    <Container ref={ref} onClick={handleOnClick} {...props}>
      <Label fontSize="sm">{t(label)}</Label>
      <StyledContainer state={currentState}>
        <StyledCircle state={currentState} />
      </StyledContainer>
    </Container>
  );
});

ToggleSwitchDouble.propTypes = {
  state: PropTypes.bool,
  label: PropTypes.string.isRequired,
  onChange: PropTypes.func,
};

export default ToggleSwitchDouble;
