import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import useSelectors from 'helpers/useSelectors';
import { purchaseOrderPropType } from 'helpers/propTypes';
import DataBox from 'components/data-box';
import { selectors as userSelectors } from 'store/user';

import { cfdiStatesValues, ableParamsToFetch } from 'store/contants';
import Button from 'components/button';
import { useI18n } from 'services/i18n';
import { useModal } from 'services/modal';
import {
  SvgSyncSolid,
} from 'react-line-awesome-svg';
import {
  actions as relationshipActions,
  selectors as relationshipSelectors,
} from 'store/relationship';

import {
  actions as docsActions,
  selectors as docsSelectors,
} from 'store/documents';

import TableBox, { tableTypes, tableSizes } from 'components/table-box';
import DocsUploader from 'components/docs-uploader';
import Accordion from 'primitive-components/accordion';
import Form, { fieldTypes } from 'components/form';

const defaultParams = { page: 1 };

const RelateDocs = ({ doc }) => {
  const [relDocsPage, setRelDocsPage] = useState(1);
  const [filterParams, setParams] = useState(defaultParams);

  const t = useI18n();
  const dispatch = useDispatch();
  const { ModalFooter } = useModal();

  const handleChangePage = (page) => setParams({ ...filterParams, page });
  const handleChangeFilters = (filters) => {
    setParams({ ...filterParams, page: 1, ...filters });

    dispatch(relationshipActions.SetSelectedItemsToRelate([]));
    dispatch(relationshipActions.SetSelectedAllToRelate(false));
  };

  const [
    rfcsIssuer,
    rfcsReceiver,

    isBusyFetchRelatedDocs,
    relatedDocs,
    paginationRelDocs,

    selectedDocsUUID,
    isBusyRelateDocs,
    isFailedRelateDocs,
    isSuccessRelateDocs,

    isBusyDocs,
    cfdis,
    paginationDocs,
  ] = useSelectors(
    userSelectors.fetchRFCs.getRFCsIssuer,
    userSelectors.fetchRFCs.getRFCsReceiver,

    relationshipSelectors.fetchRelatedDocs.getIsBusy,
    relationshipSelectors.fetchRelatedDocs.getData,
    relationshipSelectors.fetchRelatedDocs.getPagination,

    relationshipSelectors.getSelectedItems,

    relationshipSelectors.addRelatedDocs.getIsBusy,
    relationshipSelectors.addRelatedDocs.getIsFailed,
    relationshipSelectors.addRelatedDocs.getIsSuccess,

    docsSelectors.getDocs2IsBusy,
    docsSelectors.getDocs2,
    docsSelectors.getDocs2Pagination,
  );

  const { noOrdenC, type } = doc;

  const handleFetchRelatedDocs = () => dispatch(relationshipActions.fetchRelatedDocs.Call({ page: relDocsPage, noOrdenC, typedoc: type }));
  const handleFetchDocs = () => dispatch(docsActions.FetchDocs2(filterParams));

  const handleSetNoStartedRelateDocs = () => dispatch(relationshipActions.fetchRelatedDocs.NoStarted());
  const handleRelateDocs = () => {
    dispatch(relationshipActions.addRelatedDocs.Call({
      noOrdenC,
      cfdi: selectedDocsUUID,
    }));
  };

  useEffect(() => () => {
    if (isSuccessRelateDocs) handleFetchRelatedDocs();
  }, [isSuccessRelateDocs]);

  useEffect(() => () => {
    handleSetNoStartedRelateDocs();
  }, []);

  useEffect(() => {
    setRelDocsPage(1);
    setParams(defaultParams);
  }, [doc]);

  useEffect(() => {
    handleFetchRelatedDocs();
  }, [relDocsPage]);

  useEffect(() => {
    handleFetchDocs();
  }, [filterParams]);

  const formConfig = [
    {
      label: 'receiver_rfc',
      enableSearch: true,
      items: rfcsReceiver,
      fieldType: fieldTypes.DROPDOWN,
      fieldName: ableParamsToFetch.RFC_RECEIVER,
    },
    {
      label: 'issuing_rfc',
      enableSearch: true,
      items: rfcsIssuer,
      fieldType: fieldTypes.DROPDOWN,
      fieldName: ableParamsToFetch.RFC_ISSUER,
    },
    {
      label: 'state',
      enableSearch: true,
      items: cfdiStatesValues,
      fieldType: fieldTypes.DROPDOWN,
      fieldName: ableParamsToFetch.STATE,
    },
    {
      fieldType: fieldTypes.DATE_RANGE,
    },
    {
      label: 'folio',
      fieldType: fieldTypes.INPUT,
      fieldName: ableParamsToFetch.FOLIO,
    },
    {
      label: 'uuid_fiscal_folio',
      fieldType: fieldTypes.INPUT,
      fieldName: ableParamsToFetch.UUID,
    },
  ];

  const headBtnsConfigRelDocs = [
    {
      id: 'update', design: 'shadow', svgIcon: SvgSyncSolid, rounded: true, onClick: handleFetchRelatedDocs,
    },
  ];

  const headBtnsConfigDocs = [
    {
      id: 'update', design: 'shadow', svgIcon: SvgSyncSolid, rounded: true, onClick: handleFetchDocs,
    },
  ];

  const errorMessage = { type: 'error', text: 'Error relating documents' };

  return (
    <DataBox title="related_docs" separator width={['100vw', '100vw', '70vw']}>

      <DataBox title="related_docs" separator mb="20px" headBtnsConfig={headBtnsConfigRelDocs}>
        <TableBox
          tableType={tableTypes.RELATED_DOCS}
          tableSize={tableSizes.XS}
          elements={relatedDocs}
          noFoundMessage="no_related_docs_found"
          isBusy={isBusyFetchRelatedDocs}
          onChangePage={setRelDocsPage}
          pagination={paginationRelDocs}
        />
      </DataBox>
      <DataBox title="upload_documents" separator mb="20px">
        <DocsUploader />
      </DataBox>

      <DataBox
        title="document_result"
        separator
        headBtnsConfig={headBtnsConfigDocs}
      >
        <Accordion title="filter_search">
          <Form
            config={formConfig}
            value={filterParams}
            submitButton={{
              label: 'apply',
              onSubmit: handleChangeFilters,
            }}
          />
        </Accordion>

        <TableBox
          tableType={tableTypes.RELATE_DOCS}
          elements={cfdis}
          noFoundMessage="no_bills_found"
          isBusy={isBusyDocs}
          onChangePage={handleChangePage}
          pagination={paginationDocs}
        />

      </DataBox>

      <ModalFooter
        showSpinner={isBusyRelateDocs}
        message={isFailedRelateDocs && errorMessage}
      >
        <Button
          design="primary"
          rounded
          m="10px"
          p="12px 30px"
          onClick={handleRelateDocs}
          disabled={!selectedDocsUUID.length}
        >
          {t('relate_documents')}
        </Button>
      </ModalFooter>

    </DataBox>
  );
};

RelateDocs.propTypes = {
  doc: purchaseOrderPropType,
};
export default RelateDocs;
