import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Box from 'primitive-components/box';
import { useI18n } from 'services/i18n';
import { isString } from 'lodash';

import DownloadPDFIcon from 'media/pdf.png';
import DownloadXMLIcon from 'media/xml.png';
import DownloadZIPIcon from 'media/zip.png';
import RelatedDocumentsIcon from 'media/file.png';
import UnrelatedDocumentsIcon from 'media/unlink.png';
import SeeTrackingIcon from 'media/checklist.png';
import CheckSATStatusIcon from 'media/document.png';
import Trash from 'media/trash.png';
import Pencil from 'media/pencil.png';
import Refresh from 'media/refresh.png';

export const IconTypes = {
  DOWNLOAD_PDF: 'downloadPdf',
  DOWNLOAD_XML: 'downloadXml',
  DOWNLOAD_ZIP: 'downloadZip',
  SEE_TRACKING: 'seeTracking',
  CHECK_SAT: 'checkSat',
  RELATED_DOCS: 'relatedDocs',
  UNRELATED_DOCS: 'unrelatedDocs',
  DELETE: 'delete',
  EDIT: 'edit',
  REFRESH: 'refresh',
};

const StyledImg = styled('img')`
  width: 1.5rem;
  cursor: pointer;
`;

const typeToIcon = {
  [IconTypes.DOWNLOAD_PDF]: DownloadPDFIcon,
  [IconTypes.DOWNLOAD_XML]: DownloadXMLIcon,
  [IconTypes.DOWNLOAD_ZIP]: DownloadZIPIcon,
  [IconTypes.SEE_TRACKING]: SeeTrackingIcon,
  [IconTypes.CHECK_SAT]: CheckSATStatusIcon,
  [IconTypes.RELATED_DOCS]: RelatedDocumentsIcon,
  [IconTypes.UNRELATED_DOCS]: UnrelatedDocumentsIcon,
  [IconTypes.DELETE]: Trash,
  [IconTypes.EDIT]: Pencil,
  [IconTypes.REFRESH]: Refresh,
};

const typeToTooltipText = {
  [IconTypes.DOWNLOAD_PDF]: 'download_pdf',
  [IconTypes.DOWNLOAD_XML]: 'download_xml',
  [IconTypes.DOWNLOAD_ZIP]: 'download_zip',
  [IconTypes.SEE_TRACKING]: 'see_tracking',
  [IconTypes.CHECK_SAT]: 'check_sat',
  [IconTypes.RELATED_DOCS]: 'related_docs',
  [IconTypes.UNRELATED_DOCS]: 'unrelate_docs',
  [IconTypes.DELETE]: 'delete',
  [IconTypes.EDIT]: 'edit',
  [IconTypes.REFRESH]: 'refresh',
};

const Icon = ({
  type, onClick, href,
}) => {
  const t = useI18n();
  const icon = typeToIcon[type];

  if (!icon) return null;

  const tooltipTitle = typeToTooltipText[type];
  const arg = href || onClick || '#';

  const button = <StyledImg src={icon} alt={type} onClick={onClick} title={t(tooltipTitle)} />;
  const link = <a href={href || '#'}>{button}</a>;

  return isString(arg) ? link : button;
};

Icon.propTypes = {
  type: PropTypes.oneOf(Object.values(IconTypes)).isRequired,
  onClick: PropTypes.func,
  href: PropTypes.string,
};

// /////////////////////////////////////////////////////////////////
const Template = ({ icons }) => (
  <Box display="flex">
    {icons?.map((icon, index) => icon && <Icon key={index} {...icon} />)}
  </Box>
);

Template.propTypes = {
  icons: PropTypes.arrayOf(
    PropTypes.oneOfType([
      PropTypes.shape(Icon.propTypes),
      PropTypes.bool,
    ]),
  ),
};

export default Template;
