import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import Page from 'components/page';
import useSearch from 'services/search/useSearch';
import { selectors as userSelectors } from 'store/user';
import DataBox from 'components/data-box';

import DocsUploader from 'components/docs-uploader';

import { roles } from '../constants';
import CFDIsTable from './CFDIsTable';
import PCsTable from './PCsTable';

const CFDIS = () => {
  const { createRoles } = useSearch();
  const isClient = useSelector(userSelectors.fetchUser.getIsClient);

  useEffect(() => {
    createRoles(roles);
  }, []);

  return (
    <Page title="cfdis" rfcsRequired>
      {!isClient && (
        <DataBox title="upload_documents" separator>
          <DocsUploader />
        </DataBox>
      )}
      <CFDIsTable />
      {isClient && <PCsTable />}
    </Page>
  );
};
export default CFDIS;
