import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import useSelectors from 'helpers/useSelectors';
import { cfdiPropType } from 'helpers/propTypes';
import DataBox from 'components/data-box';
import { actions, selectors } from 'store/relationship';
import TableBox, { tableTypes, tableSizes } from 'components/table-box';

const RelatedDocs = ({ doc }) => {
  const [page, setPage] = useState(1);
  const dispatch = useDispatch();

  const [
    isBusy,
    relatedDocs,
    pagination,
  ] = useSelectors(
    selectors.fetchRelatedDocs.getIsBusy,
    selectors.fetchRelatedDocs.getData,
    selectors.fetchRelatedDocs.getPagination,
  );

  const handleFetchRelatedDocs = () => dispatch(actions.fetchRelatedDocs.Call({ page, doc }));

  useEffect(() => {
    if (doc) {
      handleFetchRelatedDocs();
      dispatch(actions.SetRefDoc(doc)); // used in actions
    }
  }, [doc, page]);

  return (
    <DataBox title="related_docs" separator width="50rem">
      <TableBox
        tableType={tableTypes.RELATED_DOCS}
        tableSize={tableSizes.XS}
        elements={relatedDocs}
        noFoundMessage="no_related_docs_found"
        isBusy={isBusy}
        onChangePage={setPage}
        pagination={pagination}
      />
    </DataBox>
  );
};

RelatedDocs.propTypes = {
  doc: cfdiPropType,
};
export default RelatedDocs;
