import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import useSelectors from 'helpers/useSelectors';
import styled from 'styled-components';
import { useI18n } from 'services/i18n';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';
import Separator from 'primitive-components/separator';
import Avatar from '@material-ui/core/Avatar';
import Popover from '@material-ui/core/Popover';
import { actions as authActions } from 'store/auth';
import { selectors as userSelectors } from 'store/user';

import { makeStyles } from '@material-ui/core';
import { push, getLocation } from 'connected-react-router';
import { PATH as pathProfileBase } from 'pages/profile';
import { getInitials } from 'helpers/general';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    '& > *': {
      margin: theme.spacing(1),
    },
  },
  large: {
    width: '50px',
    height: '50px',
  },
  xlarge: {
    width: '80px',
    height: '80px',
    margin: '0 auto 10px',
  },
  paper: {
    width: '250px',
  },
}));

const ContentContainer = styled(Box)`
    display: flex;
    flex-direction: column;
    box-shadow:${({ theme }) => theme.shadows.xl};
    background-color: white;
    padding:1rem 0;
`;

const StyledItem = styled(Box)`
  padding: 0.5rem 1.4rem;
`;
const StyledItemText = styled(Text)`
    cursor: pointer;
    white-space: nowrap;
    font-weight: 600;
    font-size: 0.85rem;
    color:${({ theme }) => theme.colors.profileItemText || '#000000'};
`;

const Profile = () => {
  const [anchorEl, setAnchorEl] = useState(null);
  const dispatch = useDispatch();
  const classes = useStyles();
  const t = useI18n();

  const [
    { pathname },
    userName,
  ] = useSelectors(
    getLocation,
    userSelectors.fetchUser.getUserName,
  );

  const handleOpen = (event) => setAnchorEl(event.currentTarget);
  const handleClose = () => setAnchorEl(null);

  const baseRoute = `/${pathname.split('/')[1]}`;
  const routeToProfile = `${baseRoute}${pathProfileBase}`;

  const items = [
    { id: 'my_account', onClick: () => dispatch(push(routeToProfile)) },
    { id: 'my_subscription', onClick: () => {} },
    { id: 'logout', separator: true, onClick: () => dispatch(authActions.signOut.Call()) },
  ];

  return (
    <>
      <Avatar
        aria-controls="profile-popover"
        onClick={handleOpen}
        className={classes.large}
      >
        {getInitials(userName)}
      </Avatar>

      <Popover
        id="profile-popover"
        anchorEl={anchorEl}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        open={Boolean(anchorEl)}
        onClose={handleClose}
        classes={{ paper: classes.paper }}
      >
        <ContentContainer>
          <Box p="0.5rem" centeredContent>
            <Avatar className={classes.xlarge}>
              {getInitials(userName)}
            </Avatar>
          </Box>
          {items?.map(({ id, separator, onClick }) => (
            <StyledItem key={id}>
              {separator && <Separator margin="1rem 0" />}
              <StyledItemText
                id={id}
                onClick={() => { onClick(); handleClose(); }}
                fontSize="xs"
                fontWeight="bld"
              >
                {t(id)}
              </StyledItemText>
            </StyledItem>
          ))}
        </ContentContainer>
      </Popover>
    </>
  );
};

export default Profile;
