import React, { forwardRef } from 'react';
import PropTypes from 'prop-types';
import Text from 'primitive-components/text';
import Box from 'primitive-components/box';
import Input from 'primitive-components/input';
import styled from 'styled-components';
import { styledSystem } from 'services/theme/helpers';

const Container = styled(Box)`
    display: flex;
    flex-direction: column;
    padding: 0 15px;
    margin-bottom: 14px;
    ${styledSystem};
`;

const Label = styled(Text)`
    margin-bottom: 7px;
`;

const StyledInput = styled(Input)`
   padding: 0.5rem;
   border-radius: 4px;
   border: solid 1px;
   border-color: ${({ theme, error }) => (error ? 'red' : theme.colors.inputBorder)};
   color: ${({ theme }) => theme.colors.inputText || '#000000'};
   line-height: 1.25;
   transition: border-color .15s ease-in-out;
   &:focus {
       border-color:${({ theme, error }) => (error ? 'red' : theme.colors.inputBorderFocus)};
       outline: none;
   }
`;
const InputC = forwardRef((props, ref) => {
  const {
    type, min, errorMessage, error, max, label, value, disabled, onChange, onFocus, onBlur, onClick, readOnly, placeholder, ...remainderProps
  } = props;

  return (
    <Container {...remainderProps}>
      <Label fontSize="sm">{label}</Label>
      <StyledInput
        ref={ref}
        type={type}
        readOnly={readOnly}
        disabled={disabled}
        value={value}
        onChange={onChange}
        onFocus={onFocus}
        onBlur={onBlur}
        onClick={onClick}
        placeholder={placeholder}
        min={min}
        max={max}
        error={error}
      />
      {errorMessage && <Label fontSize="xs" color="red">{errorMessage}</Label>}

    </Container>
  );
});

InputC.defaultProps = {
  value: '',
};

InputC.propTypes = {
  type: PropTypes.string,
  label: PropTypes.string,
  value: PropTypes.string,
  onChange: PropTypes.func,
  onFocus: PropTypes.func,
  onBlur: PropTypes.func,
  onClick: PropTypes.func,
  readOnly: PropTypes.bool,
  disabled: PropTypes.bool,
  placeholder: PropTypes.string,
  min: PropTypes.string,
  max: PropTypes.string,
  error: PropTypes.bool,
  errorMessage: PropTypes.string,
};

export default InputC;
