import React, { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useI18n } from 'services/i18n';
import useSelectors from 'helpers/useSelectors';
import DataBox from 'components/data-box';
import Form, { fieldTypes } from 'components/form';
import { useModal } from 'services/modal';
import { some } from 'lodash';

import {
  ableProvidersFields,
} from 'store/contants';

import { selectors as userSelectors, actions as userActions } from 'store/user';
import { selectors as usersSelectors, actions as usersActions } from 'store/users';

import Button from 'components/button';

const errorMatchEmailWithUser = {
  error: true,
  errorMessage: 'email_exist_already',
};

const NewProvider = () => {
  const [currentProvider, setCurrentProvider] = useState({});
  const [errorMatchWithUser, setErrorMatchWithUser] = useState(false);

  const dispatch = useDispatch();
  const t = useI18n();

  const { onClose: onModalClose, ModalFooter } = useModal();

  const [
    addProviderIsBusy,
    addProviderIsSuccess,

    users,
  ] = useSelectors(
    userSelectors.addProviders.getIsBusy,
    userSelectors.addProviders.getIsSuccess,

    usersSelectors.fetch.getData,
  );

  const { adminUser: { email, name } = {}, entityName, rfcsList } = currentProvider || {};

  const disabledSubmitButton = errorMatchWithUser || !email || !name || !entityName || !rfcsList?.length;

  const handleAddProvider = () => dispatch(userActions.addProviders.Call(currentProvider));
  const handleOnChange = (newCurrentProvider) => {
    setCurrentProvider(newCurrentProvider);
  };

  const matchEmailWithCreatedUser = () => ((users && email) ? some(users, ({ userEmail }) => userEmail === email) : false);
  useEffect(() => {
    setErrorMatchWithUser(matchEmailWithCreatedUser(currentProvider));
  }, [currentProvider]);

  useEffect(() => {
    if (!users) dispatch(usersActions.fetch.Call());
  }, [users]);

  useEffect(() => {
    if (addProviderIsSuccess) {
      dispatch(userActions.fetchProviders.Call());
      onModalClose();
    }
  }, [addProviderIsSuccess]);

  const formConfig = [
    {
      label: 'entity_name',
      fieldType: fieldTypes.INPUT,
      fieldName: ableProvidersFields.ENTITY_NAME,
    },
    {
      label: 'admin_name',
      fieldType: fieldTypes.INPUT_FIELD,
      fieldName: ableProvidersFields.ADMIN_USER,
      ownField: 'name',
    },
    {
      label: 'admin_email',
      fieldType: fieldTypes.INPUT_FIELD,
      fieldName: ableProvidersFields.ADMIN_USER,
      ownField: 'email',
      ...(errorMatchWithUser ? errorMatchEmailWithUser : {}),
    },
    {
      label: 'rfcs',
      fieldType: fieldTypes.ADD_RFCS,
      fieldName: ableProvidersFields.RFC_LIST,
    },
  ];

  return (
    <>
      <DataBox title="new_provider" separator width="35rem">
        <Form
          config={formConfig}
          value={currentProvider}
          onChange={handleOnChange}
        />
      </DataBox>
      <ModalFooter showSpinner={addProviderIsBusy}>
        <Button
          design="primary"
          rounded
          m="10px"
          p="12px 30px"
          onClick={handleAddProvider}
          disabled={disabledSubmitButton}
        >
          {t('upload')}
        </Button>
      </ModalFooter>
    </>
  );
};

export default NewProvider;
