import React, { useState, useEffect } from 'react';
import { useI18n } from 'services/i18n';
import PropTypes from 'prop-types';
import { Calendar } from 'react-date-range';
import Input from 'components/input';
import Popover from '@material-ui/core/Popover';

import Box from 'primitive-components/box';
import styled from 'styled-components';
import { ToShortDate } from 'helpers/date';

import 'react-date-range/dist/styles.css'; // main style file
import 'react-date-range/dist/theme/default.css'; // theme css file

const DateRangeContainer = styled(Box)`
    border: solid 1px gray;
    border-radius: 4px;
    padding: 1rem;
    background-color: white;
`;

const DateCmp = ({ value, onChange, ...remainigProps }) => {
  const [date, setDate] = useState(new Date());
  const [anchorEl, setAnchorEl] = useState(null);
  const t = useI18n();

  const handleOpen = (event) => setAnchorEl(event.currentTarget);
  const handleClose = () => setAnchorEl(null);

  const handleOnChange = (newDate) => {
    setDate(newDate);
    onChange?.({ date: newDate.toISOString() });
  };

  useEffect(() => {
    setDate(new Date(value));
  }, [value]);

  const dateToShow = ToShortDate(date);
  return (
    <>
      <Input
        label={t('date')}
        value={dateToShow}
        readOnly
        onClick={handleOpen}
        {...remainigProps}
      />
      <Popover
        anchorEl={anchorEl}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <DateRangeContainer>
          <Calendar
            date={date}
            onChange={handleOnChange}
          />
        </DateRangeContainer>
      </Popover>
    </>
  );
};

DateCmp.propTypes = {
  onChange: PropTypes.func,
  value: PropTypes.string,
};

export default DateCmp;
