import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import useSelectors from 'helpers/useSelectors';
import useSearch from 'services/search/useSearch';

import {
  actions as docsActions,
  selectors as docsSelectors,
} from 'store/documents';

import { selectors as userSelectors } from 'store/user';

import DataBox from 'components/data-box';
import Accordion from 'primitive-components/accordion';
import Form, { fieldTypes } from 'components/form';
import TableBox, { tableTypes } from 'components/table-box';
import {
  SvgSyncSolid,
} from 'react-line-awesome-svg';
import {
  cfdiStatesValues,
  paramsToFetchEveryDocsExceptPC,
  ableParamsToFetch,
} from 'store/contants';

import { roles } from '../constants';

const role = roles[0];

const CFDISTable = () => {
  const dispatch = useDispatch();
  const { search: { [role]: search = '' }, params: { [role]: params = {} }, updateParams: updateParamsT } = useSearch();
  const updateParams = (p) => updateParamsT(p, role);

  const [
    rfcsIssuer,
    rfcsReceiver,

    cfdis,
    isBusy,
    pagination,

    isClient,
  ] = useSelectors(
    userSelectors.fetchRFCs.getRFCsIssuer,
    userSelectors.fetchRFCs.getRFCsReceiver,

    docsSelectors.getDocs1,
    docsSelectors.getDocs1IsBusy,
    docsSelectors.getDocs1Pagination,

    userSelectors.fetchUser.getIsClient,
  );

  const tableType = isClient ? tableTypes.CFDI : tableTypes.PROVIDER_CFDIS;

  const paramsTofetch = isClient ? paramsToFetchEveryDocsExceptPC : {}; // Except PC if Client
  const FetchDocs = () => dispatch(docsActions.FetchDocs1({
    ...params,
    ...paramsTofetch,
  }));

  const handleChangePage = (pageT) => updateParams({ page: pageT });
  const handleChangeFilters = (filters) => updateParams(filters);

  useEffect(() => {
    FetchDocs();
  }, [search]);

  const formConfig = [
    {
      label: 'receiver_rfc',
      enableSearch: true,
      items: rfcsReceiver,
      fieldType: fieldTypes.DROPDOWN,
      fieldName: ableParamsToFetch.RFC_RECEIVER,
    },
    {
      label: 'issuing_rfc',
      enableSearch: true,
      items: rfcsIssuer,
      fieldType: fieldTypes.DROPDOWN,
      fieldName: ableParamsToFetch.RFC_ISSUER,
    },
    {
      label: 'state',
      enableSearch: true,
      items: cfdiStatesValues,
      fieldType: fieldTypes.DROPDOWN,
      fieldName: ableParamsToFetch.STATE,
    },
    {
      fieldType: fieldTypes.DATE_RANGE,
    },
    {
      label: 'uuid_fiscal_folio',
      fieldType: fieldTypes.INPUT,
      fieldName: ableParamsToFetch.UUID,
    },
  ];

  const headBtnsConfig = [
    {
      id: 'update', design: 'shadow', svgIcon: SvgSyncSolid, rounded: true, onClick: FetchDocs,
    },
  ];

  return (
    <DataBox
      title="cfdis"
      headBtnsConfig={headBtnsConfig}
      separator
    >
      <Accordion title="filter_search">
        <Form
          config={formConfig}
          value={params}
          submitButton={{
            label: 'apply',
            onSubmit: handleChangeFilters,
          }}
        />
      </Accordion>
      <TableBox
        tableType={tableType}
        elements={cfdis}
        noFoundMessage="no_bills_found"
        isBusy={isBusy}
        onChangePage={handleChangePage}
        pagination={pagination}
      />
    </DataBox>
  );
};

export default CFDISTable;
