import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';
import useSelectors from 'helpers/useSelectors';

import { useI18n } from 'services/i18n';
import styled from 'styled-components';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';
import { selectors as userSelectors, actions as userActions } from 'store/user';

const Container = styled(Box)`
  display: flex;
  flex-direction: column;
  justify-content: center;
  & > div {
    margin-bottom: 2rem;
  }
`;

const Title = styled(Text)`
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 15px 30px 15px;
`;

const Page = ({ title, rfcsRequired, children }) => {
  const t = useI18n();
  const dispatch = useDispatch();

  const [
    rfcs,
  ] = useSelectors(
    userSelectors.fetchRFCs.getData,
  );

  useEffect(() => {
    if (rfcsRequired && !rfcs) dispatch(userActions.fetchRFCs.Call());
  }, [rfcs, rfcsRequired]);

  return (
    <Container>
      <Title fontSize="lg" fontWeight="bld">
        {t(title)}
      </Title>
      {children}
    </Container>
  );
};

Page.propTypes = {
  title: PropTypes.string,
  rfcsRequired: PropTypes.bool,
  children: PropTypes.node.isRequired,
};

export default Page;
