import { createSelector } from 'reselect';
import { combineReducers } from 'redux';
import { call, put } from 'redux-saga/effects';
import RS from 'helpers/RS';
import { getPagination } from '../helpers';

const NAME = 'relationship';

/// ///////////////////////////////////////////////////////////////

const SELECTED_ITEMS = 'selectedItems';
const SET_SELECTED_ITEMS_TO_RELATE = `${SELECTED_ITEMS}.SET_SELECTED_ITEMS_TO_RELATE`;
const SetSelectedItemsToRelate = (data) => ({
  type: SET_SELECTED_ITEMS_TO_RELATE,
  payload: data,
});

const SelectedItemsReducer = {
  [SELECTED_ITEMS]: (
    (state = [], action = {}) => {
      switch (action.type) {
        case SET_SELECTED_ITEMS_TO_RELATE:
          return action.payload;
        default:
          return state;
      }
    }
  ),
};

const getSelectedItems = (state) => state[NAME][SELECTED_ITEMS] || [];

/// ///////////////////////////////////////////////////////////////

const SELECTED_ALL = 'selectedAll';
const SET_SELECTED_ALL_TO_RELATE = `${SELECTED_ALL}.SET_SELECTED_ALL_TO_RELATE`;
const SetSelectedAllToRelate = (data) => ({
  type: SET_SELECTED_ALL_TO_RELATE,
  payload: data,
});

const SelectedAllReducer = {
  [SELECTED_ALL]: (
    (state = false, action = {}) => {
      switch (action.type) {
        case SET_SELECTED_ALL_TO_RELATE:
          return action.payload;
        default:
          return state;
      }
    }
  ),
};

const getSelectedAll = (state) => state[NAME][SELECTED_ALL];

/// ///////////////////////////////////////////////////////////////

const REF_DOC = 'refDoc';
const SET_REF_DOC = `${REF_DOC}.SET_REF_DOC`;
const SetRefDoc = (data) => ({
  type: SET_REF_DOC,
  payload: data,
});

const RefDocReducer = {
  [REF_DOC]: (
    (state = null, action = {}) => {
      switch (action.type) {
        case SET_REF_DOC:
          return action.payload;
        default:
          return state;
      }
    }
  ),
};

const getRefDoc = (state) => state[NAME][REF_DOC];

/// ////////////////////////////////////////////////////////////////////////////////

function* addRelatedDocs(api, { payload: params }) {
  const response = yield call(api.relateDocs, { params });
  yield put(SetSelectedItemsToRelate([]));
  yield put(SetSelectedAllToRelate(false));
  return response;
}

const addRelatedDocsRS = RS({
  nestedPath: [NAME],
  storeName: 'addRelatedDocs',
  tryGenerator: addRelatedDocs,
});

// /////////////////////////////////////////////////////////////////////////////////

function* fetchRelatedDocs(api, { payload: { page, doc } = {} }) {
  const { uuid, type, noOrdenC } = doc;
  const params = {
    page, uuid, noOrdenC, typedoc: type,
  };
  return yield call(api.fetchRelatedDocs, { params });
}

const fetchRelatedDocsRS = RS({
  nestedPath: [NAME],
  storeName: 'fetchRelatedDocs',
  tryGenerator: fetchRelatedDocs,
});

const getRelatedDocsData = fetchRelatedDocsRS.selectors.getData;
const getRelatedDocs = createSelector(
  getRelatedDocsData,
  (data) => data?.list || [],
);

const getRelatedDocsPagination = createSelector(
  getRelatedDocsData,
  (data) => getPagination(data),
);

// /////////////////////////////////////////////////////////////////////////////////

function* removeRelatedDocs(api, { payload: { refDoc, doc } = {} }) {
  const { noOrdenC } = refDoc;
  const { uuid } = doc;
  if (!noOrdenC || !uuid) {
    console.error('Does not have valid params');
    return null;
  }
  const params = {
    noOrdenC,
    cfdiDelete: [uuid],
  };
  return yield call(api.removeRelationship, { params });
}

const removeRelatedDocsRS = RS({
  nestedPath: [NAME],
  storeName: 'removeRelatedDocs',
  tryGenerator: removeRelatedDocs,
});

// /////////////////////////////////////////////////////////////////////////////////

export const sagas = [
  addRelatedDocsRS.sagas,
  fetchRelatedDocsRS.sagas,
  removeRelatedDocsRS.sagas,
];

export const reducer = {
  [NAME]: combineReducers({
    ...SelectedItemsReducer,
    ...SelectedAllReducer,
    ...RefDocReducer,

    ...addRelatedDocsRS.reducer,
    ...fetchRelatedDocsRS.reducer,
    ...removeRelatedDocsRS.reducer,
  }),
};

export const actions = {
  SetSelectedItemsToRelate,
  SetSelectedAllToRelate,
  SetRefDoc,

  addRelatedDocs: addRelatedDocsRS.actions,
  fetchRelatedDocs: fetchRelatedDocsRS.actions,
  removeRelatedDocs: removeRelatedDocsRS.actions,
};

// Selectors
export const selectors = {
  getSelectedItems,
  getSelectedAll,
  getRefDoc,

  fetchRelatedDocs: {
    getIsBusy: fetchRelatedDocsRS.selectors.getIsBusy,
    getIsFailed: fetchRelatedDocsRS.selectors.getIsFailed,
    getIsSuccess: fetchRelatedDocsRS.selectors.getIsSuccess,
    getIsNotStarted: fetchRelatedDocsRS.selectors.getIsNotStarted,
    getData: getRelatedDocs,
    getPagination: getRelatedDocsPagination,
  },
  addRelatedDocs: addRelatedDocsRS.selectors,
  removeRelatedDocs: removeRelatedDocsRS.selectors,
};
