import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { layout, border, space } from 'styled-system';
import Box from 'primitive-components/box';
import Logo from 'components/logo';
import { useMediaQuery, useTheme } from '@material-ui/core';
import { useDispatch } from 'react-redux';
import { Collapse } from 'store/layout/actions';

const LineWidth = ['1.3rem', '2rem', '1rem'];
const LineDetails = LineWidth.map((width) => ({
  width,
  marginLeft: `calc(${LineWidth[1]} - ${width})`,
}));

const StyledBannerLogoContainer = styled(Box)`
  display: flex;
  align-items: center;
  height: 100%;
  width: 100%;
`;

const StyledButton = styled(Box)`
  height: 1.4rem;
  width: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`;

const Line = styled(Box)`
  border: none;
  border-top: solid 2.5px;
  transition: all 0.5s ease;
  border-color:${({ theme }) => theme.colors.placeholderText};
  ${layout}
  ${border}
    ${space}
`;

const BannerLogo = ({ closed, onClick }) => {
  const dispatch = useDispatch();
  const theme = useTheme();
  const mobileView = useMediaQuery(theme.breakpoints.down('xs'));

  useEffect(() => {
    dispatch(Collapse(!mobileView));
  }, [mobileView]);

  return (
    <StyledBannerLogoContainer onClick={onClick}>
      <Logo
        type={closed || mobileView ? 'simple' : 'horizontal'}
        alt="bannerLogo"
        flex-shrink="0"
        height="60px"
        ml="1rem"
      />
      <StyledButton>
        {LineDetails?.map(({ width, marginLeft }, key) => (
          <Line
            key={key}
            width={width}
            marginLeft={closed ? '0' : marginLeft}
          />
        ))}
      </StyledButton>
    </StyledBannerLogoContainer>
  );
};
BannerLogo.defaultProps = {
  closed: false,
};

BannerLogo.propTypes = {
  closed: PropTypes.bool,
  onClick: PropTypes.func,
};
export default BannerLogo;
