import React from 'react';
import { docStatePropType } from 'helpers/propTypes';
import Button, { designs as buttonDesigns } from 'components/button';
import PropTypes from 'prop-types';
import { cfdiStates } from 'store/contants';
import styled from 'styled-components';
import { useModal } from 'services/modal';
import StateHistory from 'scenes/modals/state-history';

const StyledButton = styled(Button)`
  white-space: break-spaces;    
  padding: 3.5px 7px;
  text-align: left;
`;

const ButtonState = ({ data, ...remainderProps }) => {
  const { renderComponent } = useModal();
  const { estado } = data;
  const lastState = estado[estado.length - 1];

  const handleButtonClick = () => renderComponent(() => <StateHistory data={data} />);

  const isError = lastState.estado === cfdiStates.ERROR;
  const design = isError ? buttonDesigns.ERROR : buttonDesigns.LINK;
  const width = isError ? 'fit-content' : '140px';

  return (
    <StyledButton
      rounded
      fontSize="sm"
      fontWeight="sem"
      design={design}
      onClick={handleButtonClick}
      width={width}
      {...remainderProps}
    >
      {lastState.estado}
    </StyledButton>
  );
};

ButtonState.propTypes = {
  data: PropTypes.shape({
    estado: PropTypes.arrayOf(docStatePropType),
  }),
};

export default ButtonState;
