import React from 'react';
import Box from 'primitive-components/box';
import styled, { useTheme } from 'styled-components';
import PropTypes from 'prop-types';
import { SvgBriefcaseSolid, SvgUser } from 'react-line-awesome-svg';
import Icon from 'primitive-components/icon';

export const NotificationType = {
  NEW_USER: 'NEW_USER',
  NEW_PROVIDER: 'NEW_PROVIDER',
};

const icons = {
  [NotificationType.NEW_USER]: SvgUser,
  [NotificationType.NEW_PROVIDER]: SvgBriefcaseSolid,
};

const ItemContainer = styled(Box)`
    display: flex;
    align-items: center;
    padding: 1rem 0 1rem 1rem;
    font-size: 0.85rem;
    color: #5d5386;
    width: 100%;
    position: relative;
`;

const MessageBody = styled(Box)`
    padding-left: 1rem;
    color: ${({ theme }) => theme.colors.placeholderText};
`;

const MessageHeading = styled(Box)`
    font-size: 0.85rem;
    margin-bottom: 5px;
    font-weight: 600;
    color: ${({ theme }) => theme.colors.tableTextColor};
    padding: 0;
`;

const Date = styled(Box)`
    font-size: 0.7rem;
    font-weight: 500;
    margin: 0;
    padding: 0;
`;

const NotificationItem = ({ notificationType, title, date }) => {
  const svgIcon = icons[notificationType];
  const theme = useTheme();
  return (
    <ItemContainer>
      <Box>
        <Icon svg={svgIcon} size={40} color={theme.colors.sideBarText} />
      </Box>
      <MessageBody>
        <MessageHeading>{title}</MessageHeading>
        <Date>{date}</Date>
      </MessageBody>
    </ItemContainer>
  );
};

// TODO: Mover esto para donde estan las demas props types
NotificationItem.propTypes = {
  title: PropTypes.string,
  date: PropTypes.string,
  notificationType: PropTypes.string,
};

export default NotificationItem;
