import React from 'react';
import PropTypes from 'prop-types';
import Box from 'primitive-components/box';
import { useModal } from 'services/modal';
import { useI18n } from 'services/i18n';
import Text from 'primitive-components/text';
import Button from 'components/button';
import DataBox from 'components/data-box';

const Confirm = ({
  title, message, onConfirm, isBusy, errorMessage,
}) => {
  const { ModalFooter } = useModal();
  const t = useI18n();

  return (
    <DataBox title={title} width="30rem">
      <Box p="10px" centeredContent minHeight="10rem">
        <Text>{t(message)}</Text>
      </Box>
      <ModalFooter
        showSpinner={isBusy}
        closeButtonLabel="cancel"
        message={!!errorMessage && ({ type: 'error', text: errorMessage })}
      >
        <Button
          design="primary"
          rounded
          m="10px"
          p="12px 30px"
          onClick={onConfirm}
          disabled={isBusy}
        >
          {t('accept')}
        </Button>
      </ModalFooter>
    </DataBox>
  );
};

Confirm.propTypes = {
  title: PropTypes.string.isRequired,
  message: PropTypes.string.isRequired,
  onConfirm: PropTypes.func,
  isBusy: PropTypes.bool,
  errorMessage: PropTypes.string,
};

export default Confirm;
