import React, { useEffect } from 'react';
import Page from 'components/page';
import DataBox from 'components/data-box';
import { useModal } from 'services/modal';
import useSelectors from 'helpers/useSelectors';
import { useHasRoles } from 'services/hasRoles';
import Accordion from 'primitive-components/accordion';
import Form, { fieldTypes } from 'components/form';
import useSearch from 'services/search/useSearch';
import { useDispatch } from 'react-redux';
import { ableParamsToFetch } from 'store/contants';
import TableBox, { tableTypes } from 'components/table-box';

import { actions as userActions, selectors as userSelectors } from 'store/user';
import NewProvider from 'scenes/modals/new-provider';

import {
  SvgPlusSolid,
  // SvgUploadSolid,
  // SvgDownloadSolid,
  SvgSyncSolid,
} from 'react-line-awesome-svg';

const Providers = () => {
  const dispatch = useDispatch();
  const { params, updateParams } = useSearch();

  const { renderComponent } = useModal();

  const [
    providers,
    providersIsBusy,
  ] = useSelectors(
    userSelectors.fetchProviders.getData,
    userSelectors.fetchProviders.getIsBusy,
  );

  const handleShowModalNewProvider = () => renderComponent(NewProvider, { hideFooter: true });
  const handleFetchUserProviders = () => dispatch(userActions.fetchProviders.Call());

  useEffect(() => {
    if (!providers) handleFetchUserProviders();
  }, [providers]);

  const { provider: { full, write } } = useHasRoles();

  const headBtnsConfig = [
    (full || write) && {
      id: 'new', design: 'primary', svgIcon: SvgPlusSolid, onClick: handleShowModalNewProvider,
    },
    // { id: 'import', design: 'default', svgIcon: SvgUploadSolid },
    // { id: 'export', design: 'info', svgIcon: SvgDownloadSolid },
    {
      id: 'update', design: 'shadow', svgIcon: SvgSyncSolid, rounded: true, onClick: handleFetchUserProviders,
    },
  ];

  const formConfig = [
    {
      label: 'receiver_rfc', // RFC
      enableSearch: true,
      items: [],
      fieldType: fieldTypes.DROPDOWN,
      fieldName: ableParamsToFetch.RFC_RECEIVER,
    },
    {
      label: 'user',
      fieldType: fieldTypes.INPUT,
      // fieldName: ableParamsToFetch.RFC_RECEIVER,
    },
    {
      label: 'email',
      fieldType: fieldTypes.INPUT,
      // fieldName: ableParamsToFetch.RFC_RECEIVER,
    },
    {
      label: 'social_reason',
      fieldType: fieldTypes.INPUT,
      // fieldName: ableParamsToFetch.RFC_RECEIVER,
    },
  ];

  const handleChangeFilters = (filters) => updateParams(filters);

  return (
    <Page title="providers">
      <DataBox title="providers_management" separator headBtnsConfig={headBtnsConfig}>
        <Accordion title="filter_search">
          <Form
            config={formConfig}
            value={params}
            submitButton={{
              label: 'apply',
              onSubmit: handleChangeFilters,
            }}
          />
        </Accordion>

        <TableBox
          tableType={tableTypes.PROVIDER_MANAGEMENT}
          elements={providers}
          noFoundMessage="no_providers_found"
          isBusy={providersIsBusy}
        />
      </DataBox>

    </Page>

  );
};

export default Providers;
