import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { ToShortDate } from 'helpers/date';
import { useSelector, useDispatch } from 'react-redux';
import { useModal } from 'services/modal';
import { formatNumberToCurrency } from 'helpers/general';
import useSelectors from 'helpers/useSelectors';
import { actions, selectors } from 'store/relationship';
import { docsTypes } from 'store/contants';
import Confirm from 'scenes/modals/confirm';

import DownloadZip from 'scenes/modals/download-zip';
import ActionsTemplate, { IconTypes } from '../ActionsTemplate';
import ButtonState from '../ButtonState';

const RemoveRelatedDocsConfirm = ({ doc, refDoc }) => {
  const dispatch = useDispatch();
  const { onClose } = useModal();

  const [
    isBusy,
    isSuccess,
    isFailed,
  ] = useSelectors(
    selectors.removeRelatedDocs.getIsBusy,
    selectors.removeRelatedDocs.getIsSuccess,
    selectors.removeRelatedDocs.getIsFailed,
  );

  useEffect(() => () => dispatch(actions.removeRelatedDocs.NoStarted()), []);

  useEffect(() => {
    if (isSuccess) {
      onClose();
      dispatch(actions.fetchRelatedDocs.Call({ page: 1, doc: refDoc }));
    }
  }, [isSuccess]);

  const handleOnConfirmRemove = () => dispatch(actions.removeRelatedDocs.Call({ refDoc, doc })); // TODO

  return (
    <Confirm
      title="remove_provider_confirm_title"
      message="remove_provider_confirm_message"
      errorMessage={isFailed && 'remove_provider_error_message'}
      isBusy={isBusy}
      onConfirm={handleOnConfirmRemove}
    />
  );
};

RemoveRelatedDocsConfirm.propTypes = {
  doc: PropTypes.objectOf(PropTypes.any),
  refDoc: PropTypes.objectOf(PropTypes.any),
};

const Actions = ({ data = {} }) => {
  const { renderComponent } = useModal();

  const { addressXml, addressPdf, type: currentDocType } = data;
  const hasFilesToDownload = addressXml && addressPdf;

  const refDoc = useSelector(selectors.getRefDoc);
  const { type: refDocType } = refDoc;

  const ableRemoveRelatedDoc = (refDocType === docsTypes.PURCHASE) && (currentDocType === docsTypes.BILL);

  const RemoveRelatedDocConfirmWrapper = () => <RemoveRelatedDocsConfirm doc={data} refDoc={refDoc} />;
  const handleShowConfirmRemoveRelatedDoc = () => renderComponent(RemoveRelatedDocConfirmWrapper, { hideFooter: true });
  const handleDownloadZip = () => renderComponent(() => <DownloadZip data={data} />);

  const iconsData = [
    hasFilesToDownload && { type: IconTypes.DOWNLOAD_PDF, href: addressPdf },
    hasFilesToDownload && { type: IconTypes.DOWNLOAD_XML, href: addressXml },
    hasFilesToDownload && { type: IconTypes.DOWNLOAD_ZIP, onClick: handleDownloadZip },
    ableRemoveRelatedDoc && { type: IconTypes.UNRELATED_DOCS, onClick: handleShowConfirmRemoveRelatedDoc },
  ];

  return (
    <ActionsTemplate icons={iconsData} />
  );
};

export const heads = [
  'actions',
  'serie',
  'folio',
  'date',
  'issuing_rfc',
  'receiver_rfc',
  'state',
  'total',
  'uuid_fiscal_folio',
];

export const transmute = (data) => {
  const {
    uuid, serie, folio, date, rfcIssuer, rfcReceiver, total,
  } = data;

  return [
    <Actions data={data} />,
    serie,
    folio,
    ToShortDate(date, 'string'),
    rfcIssuer,
    rfcReceiver,
    <ButtonState data={data} disabled />,
    formatNumberToCurrency(total),
    uuid,
  ];
};
