import React from 'react';
import { useModal } from 'services/modal';

import { ToShortDate } from 'helpers/date';
import { formatNumberToCurrency } from 'helpers/general';
import RelatedDocs from 'scenes/modals/related-docs';

import DownloadZip from 'scenes/modals/download-zip';
import ButtonState from '../ButtonState';

import ActionsTemplate, { IconTypes } from '../ActionsTemplate';

const Actions = ({ data = {} }) => {
  const { renderComponent } = useModal();
  const handleOpenRelatedDocs = () => renderComponent(() => <RelatedDocs doc={data} />);
  const handleDownloadZip = () => renderComponent(() => <DownloadZip data={data} />);

  const { addressPdf, addressXml } = data;

  const iconsData = [
    { type: IconTypes.RELATED_DOCS, onClick: handleOpenRelatedDocs },
    { type: IconTypes.DOWNLOAD_PDF, href: addressPdf },
    { type: IconTypes.DOWNLOAD_XML, href: addressXml },
    { type: IconTypes.DOWNLOAD_ZIP, onClick: handleDownloadZip },
  ];

  return (
    <ActionsTemplate icons={iconsData} />
  );
};

export const heads = [
  'actions',
  'serie',
  'folio',
  'date',
  'issuing_rfc',
  'receiver_rfc',
  'state',
  'total',
  'uuid_fiscal_folio',
];

export const transmute = (data) => {
  const {
    serie, date, folio, rfcIssuer, rfcReceiver, total, uuid,
  } = data;

  // console.log('transformedValidations',transformedValidations);

  return [
    <Actions data={data} />,
    serie,
    folio,
    ToShortDate(date, 'string'),
    rfcIssuer,
    rfcReceiver,
    <ButtonState data={data} />,
    formatNumberToCurrency(total),
    uuid,
  ];
};
