import React, { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useI18n } from 'services/i18n';
import DataBox from 'components/data-box';
import Form, { fieldTypes } from 'components/form';
import { useModal } from 'services/modal';
import { ableUserFields, ableProfileFields } from 'store/contants';
import Button from 'components/button';
import { actions as usersActions, selectors as usersSelectors } from 'store/users';
import { selectors as profilesSelectors, actions as profilesActions } from 'store/profiles';

import useSelectors from 'helpers/useSelectors';
import PropTypes from 'prop-types';

const defaultUser = {
  [ableUserFields.NAME]: '',
  [ableUserFields.USER_EMAIL]: '',
  [ableUserFields.USER_PROFILE]: '',
};

const NewUpdateUser = ({ user }) => {
  const [currentUser, setCurrentUser] = useState(defaultUser);

  const dispatch = useDispatch();
  const t = useI18n();

  const { onClose: onModalClose, ModalFooter } = useModal();
  const titleTag = user ? 'update_user' : 'new_user';

  const [
    profiles,

    isBusyAddUser,
    isSuccessAddUser,
    isFailedAddUser,

    isBusyUpdateUsers,
    isSuccessUpdateUsers,
    isFailedUpdateUsers,
  ] = useSelectors(
    profilesSelectors.fetch.getData,

    usersSelectors.add.getIsBusy,
    usersSelectors.add.getIsSuccess,
    usersSelectors.add.getIsFailed,

    usersSelectors.update.getIsBusy,
    usersSelectors.update.getIsSuccess,
    usersSelectors.update.getIsFailed,

  );

  useEffect(() => () => {
    dispatch(usersActions.add.NoStarted());
    dispatch(usersActions.update.NoStarted());
  }, []);

  useEffect(() => () => {
    if (!profiles?.length) dispatch(profilesActions.fetch.Call());
  }, [profiles]);

  useEffect(() => {
    if (isSuccessAddUser || isSuccessUpdateUsers) {
      dispatch(usersActions.fetch.Call());
      onModalClose();
    }
  }, [isSuccessAddUser, isSuccessUpdateUsers]);

  useEffect(() => {
    if (user) {
      const { userEmail, userProfile, name } = user;
      setCurrentUser({
        name,
        userEmail,
        // eslint-disable-next-line no-underscore-dangle
        userProfile: userProfile._id,
      });
    }
  }, [user]);

  const formConfig = [
    {
      label: 'name',
      fieldType: fieldTypes.INPUT,
      fieldName: ableUserFields.NAME,
    },
    !user && {
      label: 'email_title',
      fieldType: fieldTypes.INPUT,
      fieldName: ableUserFields.USER_EMAIL, // TODO falta validar tipo email
    },
    {
      label: 'profile',
      items: profiles,
      optionTextField: ableProfileFields.PROFILE_NAME,
      optionValueField: ableProfileFields.ID,
      enableSearch: true,
      single: true,
      fieldType: fieldTypes.DROPDOWN,
      fieldName: ableUserFields.USER_PROFILE,
    },
  ];

  const handleAddUser = () => dispatch(usersActions.add.Call(currentUser));
  const handleUpdateUsers = () => dispatch(usersActions.update.Call(currentUser));
  const handleOnSubmit = user ? handleUpdateUsers : handleAddUser;

  const isFailed = isFailedAddUser || isFailedUpdateUsers;
  const isSuccess = isSuccessAddUser || isSuccessUpdateUsers;

  const message = (isFailed || isSuccess) && {
    type:
      (isFailed && 'error')
      || (isSuccess && 'success')
      || 'error',
    text:
      (isFailedAddUser && 'add_user_failed_message')
      || (isSuccessAddUser && 'add_user_success_message')
      || (isFailedUpdateUsers && 'update_user_failed_message')
      || (isSuccessUpdateUsers && 'update_user_success_message')
      || 'add_user_failed_message',
  };

  return (
    <>
      <DataBox title={titleTag} separator width="35rem">
        <Form
          config={formConfig}
          value={currentUser}
          onChange={setCurrentUser}
        />
      </DataBox>
      <ModalFooter
        showSpinner={isBusyAddUser || isBusyUpdateUsers}
        message={message}
      >
        <Button design="primary" rounded m="10px" p="12px 30px" onClick={handleOnSubmit}>{t('upload')}</Button>
      </ModalFooter>
    </>
  );
};

NewUpdateUser.propTypes = {
  user: PropTypes.objectOf(PropTypes.any),
};

export default NewUpdateUser;
