import React, { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { useI18n } from 'services/i18n';

import Box from 'primitive-components/box';
import Text from 'primitive-components/text';
import Input from 'components/input';
import { rfcPropType } from 'helpers/propTypes';
import { ableRfcsFields } from 'store/contants';
import { AddCircleOutline, HighlightOff } from '@material-ui/icons';
import { remove } from 'lodash';

const Container = styled(Box)`
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 1rem;
`;

const Row = styled(Box)`
    display: flex;
    align-items: center;
    border: solid 1px;
    border-radius: 5px;
    padding-bottom: 8px;
    border-color: ${({ error }) => (error ? 'red' : 'transparent')};
`;

const StyledInput = styled(Input)`
    margin-bottom: 0;
`;

const AddRfc = ({
  value, onChange, label, ...remainderProps
}) => {
  const [rfcList, setRfcList] = useState([]);
  const [newRFC, setNewRFC] = useState({});

  const [errorNewRFC, setErrorNewRFC] = useState(false);
  const refInput = useRef(null);

  const t = useI18n();

  useEffect(() => {
    setRfcList(value);
  }, [value]);

  const handleOnChangeNewRFC = (change) => {
    setNewRFC({ ...newRFC, ...change });
    setErrorNewRFC(false);
  };

  const handleAddRFCToList = () => {
    const { code, company } = newRFC;
    if (code === '' || company === '') {
      setErrorNewRFC(true);
    }

    const newRfcList = [...rfcList];
    newRfcList.push(newRFC);
    setRfcList(newRfcList);
    onChange?.(newRfcList);

    setNewRFC({});
    refInput?.current?.focus();
  };
  const handleRemoveRFCFromList = (index) => {
    const newRfcList = [...rfcList];
    remove(newRfcList, (rfc, i) => index === i);
    setRfcList(newRfcList);
    onChange?.(newRfcList);
  };

  return (
    <Box {...remainderProps}>
      <Text ml="1rem">{t(label)}</Text>
      <Container mt="1rem">

        <Row>
          <StyledInput
            label={t('code')}
            ref={refInput}
            value={newRFC[ableRfcsFields.CODE]}
            onChange={(e) => handleOnChangeNewRFC({ [ableRfcsFields.CODE]: e.target.value.toUpperCase() })}
            error={errorNewRFC}
          />
          <StyledInput
            label={t('company')}
            value={newRFC[ableRfcsFields.COMPANY]}
            onChange={(e) => handleOnChangeNewRFC({ [ableRfcsFields.COMPANY]: e.target.value })}
            error={errorNewRFC}
          />
          <Box mt="25px" onClick={handleAddRFCToList}>
            <AddCircleOutline style={{ color: 'green' }} />
          </Box>
        </Row>

        {rfcList?.map((rfc, index) => (
          <Row key={index}>
            <StyledInput
              readOnly
              value={rfc[ableRfcsFields.CODE]}
            />
            <StyledInput
              readOnly
              value={rfc[ableRfcsFields.COMPANY]}
            />
            <Box mt="10px" onClick={() => handleRemoveRFCFromList(index)}>
              <HighlightOff color="error" />
            </Box>
          </Row>
        ))}

      </Container>
    </Box>
  );
};

AddRfc.defaultProps = {
  value: [],
};

AddRfc.propTypes = {
  value: PropTypes.arrayOf(rfcPropType),
  onChange: PropTypes.func,
  label: PropTypes.string,
};

export default AddRfc;
