import React, {
  forwardRef, useState, useEffect,
} from 'react';
import PropTypes from 'prop-types';
import { useI18n } from 'services/i18n';
import styled from 'styled-components';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';
import { styledSystem } from 'services/theme/helpers';

const Container = styled(Box)`
    display: flex;
    flex-direction: column;
    padding: 0 15px;
    margin-bottom: 14px;
    ${styledSystem};
`;

const StyledContainer = styled(Box)`
position: relative;
width: 34px;
padding: 2px;
background-color: ${({ theme, state, disabled }) => {
    if (disabled) {
      return state
        ? theme.colors.toggleSwitchTrueDisabledBackground
        : theme.colors.toggleSwitchFalseDisabledBackground;
    }

    return (state
      ? theme.colors.toggleSwitchTrueBackground
      : theme.colors.toggleSwitchDisabledBackground);
  }};
border-radius: 16px;
`;

const StyledCircle = styled(Box)`
position: relative;
left: ${({ state }) => (state ? '50%' : '0')};
height: 16px;
width: 16px;
background-color: #FFFFFF;
border-radius: 50%;
`;

const Label = styled(Text)`
    margin-bottom: 7px;
`;

const ToggleSwitch = forwardRef((props, ref) => {
  const {
    state, label, onChange, disabled,
  } = props;
  const [currentState, setCurrentState] = useState(false);
  const t = useI18n();
  useEffect(() => {
    setCurrentState(state);
  }, [state]);

  const handleOnClick = () => {
    if (disabled) return;
    setCurrentState(!currentState);
    onChange(!currentState);
  };

  return (
    <Container ref={ref} onClick={handleOnClick} {...props}>
      <Label fontSize="sm">{t(label)}</Label>
      <StyledContainer state={currentState} disabled={disabled}>
        <StyledCircle state={currentState} />
      </StyledContainer>
    </Container>
  );
});

ToggleSwitch.defaultProps = {
  state: false,
};

ToggleSwitch.propTypes = {
  state: PropTypes.bool,
  disabled: PropTypes.bool,
  label: PropTypes.string.isRequired,
  onChange: PropTypes.func,
};

export default ToggleSwitch;
