import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { useI18n } from 'services/i18n';
import useSelectors from 'helpers/useSelectors';
import DataBox from 'components/data-box';
import Form, { fieldTypes } from 'components/form';
import { useModal } from 'services/modal';
import { ablePurchaseOrderFields, docsTypes } from 'store/contants';
import { selectors as userSelectors } from 'store/user';

import Button from 'components/button';
import {
  actions as addPurchaseOrderActions,
  selectors as addPurchaseOrderSelectors,
} from 'store/add-purchase-order';

const defaultPurchase = {
  [ablePurchaseOrderFields.TYPE]: docsTypes.PURCHASE,
  [ablePurchaseOrderFields.DATE]: new Date(),
};

const NewPurchaseOrder = () => {
  const [purchase, setPurchase] = useState(defaultPurchase);
  const dispatch = useDispatch();
  const t = useI18n();
  const { onClose, ModalFooter } = useModal();

  const [
    rfcsIssuer,
    rfcsReceiver,

    isBusy,
    isSuccess,
    isFailed,
  ] = useSelectors(
    userSelectors.fetchRFCs.getRFCsIssuer,
    userSelectors.fetchRFCs.getRFCsReceiver,

    addPurchaseOrderSelectors.getIsBusy,
    addPurchaseOrderSelectors.getIsSuccess,
    addPurchaseOrderSelectors.getIsFailed,
  );

  useEffect(() => () => dispatch(addPurchaseOrderActions.NoStarted()), []);

  useEffect(() => {
    if (isSuccess) onClose();
  }, [isSuccess]);

  const handleUploadPurchaseOrder = () => dispatch(addPurchaseOrderActions.Call(purchase));

  const disabledSubmit = isBusy
  || !purchase[ablePurchaseOrderFields.PURCHASE_ORDER]
  || !purchase[ablePurchaseOrderFields.RFC_ISSUER]
  || !purchase[ablePurchaseOrderFields.RFC_RECEIVER];

  const formConfig = [
    {
      label: 'number',
      fieldType: fieldTypes.INPUT,
      type: 'number',
      fieldName: ablePurchaseOrderFields.PURCHASE_ORDER,
    },
    {
      label: 'issuing_rfc',
      enableSearch: true,
      items: rfcsIssuer,
      fieldType: fieldTypes.DROPDOWN,
      single: true,
      fieldName: ablePurchaseOrderFields.RFC_ISSUER,
    },
    {
      label: 'total',
      fieldType: fieldTypes.INPUT,
      type: 'number',
      fieldName: ablePurchaseOrderFields.TOTAL,
    },
    {
      label: 'receiver_rfc',
      enableSearch: true,
      items: rfcsReceiver,
      fieldType: fieldTypes.DROPDOWN,
      single: true,
      fieldName: ablePurchaseOrderFields.RFC_RECEIVER,
    },
  ];
  const errorMessage = {
    type: 'error',
    text: 'error_inserting_purchase_order',
  };

  return (
    <>
      <DataBox title="new_purchase_order" separator width="35rem">
        <Form
          config={formConfig}
          value={purchase}
          onChange={setPurchase}
        />
      </DataBox>
      <ModalFooter
        showSpinner={isBusy}
        message={isFailed && errorMessage}
      >
        <Button
          design="primary"
          rounded
          m="10px"
          p="12px 30px"
          onClick={handleUploadPurchaseOrder}
          disabled={disabledSubmit}
        >
          {t('upload')}
        </Button>
      </ModalFooter>
    </>
  );
};

export default NewPurchaseOrder;
