import React, { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useI18n } from 'services/i18n';
import useSelectors from 'helpers/useSelectors';
import DataBox from 'components/data-box';
import Form, { fieldTypes } from 'components/form';
import { useModal } from 'services/modal';
import { ableShareFields } from 'store/contants';
import {
  actions as shareReportActions,
  selectors as shareReportSelectors,
} from 'store/share-report';

import Button from 'components/button';
import { getPdfAsBase64 } from 'helpers/reports';
import { selectors as userSelectors } from 'store/user';

const defaultShare = {
  [ableShareFields.SHARE_CURRENT_EMAIL]: true,
  [ableShareFields.SHARE_CUSTOM_EMAIL]: [],
};

const ShareReport = ({ element }) => {
  const [currentShare, setCurrentShare] = useState(defaultShare);

  const dispatch = useDispatch();
  const t = useI18n();

  const { onClose: onModalClose, ModalFooter } = useModal();

  const [
    isBusyShare,
    isSuccessShare,
    isFailedShare,
    userEmail,
  ] = useSelectors(
    shareReportSelectors.getShareIsBusy,
    shareReportSelectors.getShareIsSuccess,
    shareReportSelectors.getShareIsFailed,
    userSelectors.fetchUser.getUserEmail,
  );

  const handleShare = () => {
    const date = new Date();
    const currentDate = `${date.getDate()}-${date.getMonth() + 1}-${date.getFullYear()}`;
    const promise = getPdfAsBase64(element, `Reports-${currentDate}`);
    // TODO: Aqui segun el valor del checkbox mandar el email del current user o el email en el input
    promise.then((pdfAsString) => {
      dispatch(shareReportActions.ShareReport({ email: userEmail, pdf: pdfAsString }));
    });
  };

  useEffect(() => {
    if (isSuccessShare) {
      onModalClose();
    }
  }, [isSuccessShare]);

  // TODO: Borrar el texto del email si se marca el checkbox
  const handleOnChange = (value) => {
    setCurrentShare(value);
  };

  const formConfig = [
    {
      label: 'currentEmail',
      fieldType: fieldTypes.CHECKBOX,
      fieldName: ableShareFields.SHARE_CURRENT_EMAIL,
    },
    {
      label: 'customEmail',
      fieldType: fieldTypes.INPUT,
      disabled: currentShare[ableShareFields.SHARE_CURRENT_EMAIL],
      fieldName: ableShareFields.SHARE_CUSTOM_EMAIL,
    },
  ];

  const errorMessage = { type: 'error', text: 'Error compartiendo reporte' };

  return (
    <>
      <DataBox title="share" separator width="35rem">
        <Form
          config={formConfig}
          value={currentShare}
          onChange={handleOnChange}
        />
      </DataBox>
      <ModalFooter showSpinner={isBusyShare} message={isFailedShare && errorMessage}>
        <Button design="primary" rounded m="10px" p="12px 30px" onClick={handleShare}>{t('share')}</Button>
      </ModalFooter>
    </>
  );
};

export default ShareReport;
