import React from 'react';
import PropTypes from 'prop-types';
import DataBox from 'components/data-box';
import Box from 'primitive-components/box';
import { PieChart } from 'components/chart';
import styled from 'styled-components';

const ChartContainer = styled(Box)`
  height: 100%;
  position: relative;
  max-width: 90%;
  margin: 0 auto;
`;

const DocumentsByStatus = ({ statusReport }) => {
  const getPieChartData = () => {
    const labels = statusReport?.map(({ estado }) => estado);
    return ({
      type: 'pie',
      colors: [
        '#88ab3f',
        '#e6b52e',
        '#cc4927',
        '#747a83',
        '#4064aa',
        '#7685be',
        '#483e77',
        '#736893',
        '#a49ab8',
      ],
      data: statusReport?.map(({ total }) => ({
        labels,
        data: total,
      })),
    });
  };

  return (
    <DataBox title="documents_by_status">
      <ChartContainer centeredContent>
        {statusReport && <PieChart {...getPieChartData()} />}
      </ChartContainer>
    </DataBox>
  );
};

DocumentsByStatus.propTypes = {
  statusReport: PropTypes.arrayOf(
    PropTypes.shape({
      estado: PropTypes.string,
      total: PropTypes.number,
    }),
  ),
};
export default DocumentsByStatus;
