import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import PropTypes from 'prop-types';
import Box from 'primitive-components/box';
import { useI18n } from 'services/i18n';
import DataBox from 'components/data-box';
import styled from 'styled-components';
import Spinner from 'primitive-components/spinner';
import Text from 'primitive-components/text';
import useSelectors from 'helpers/useSelectors';
import { actions as systemActions, selectors as systemSelectors } from 'store/system';
import { useModal } from 'services/modal';
import { isString } from 'lodash';

const DownloadLink = styled.a`
`;

const DownloadZip = ({ data }) => {
  const t = useI18n();
  const dispatch = useDispatch();
  const { onClose: onModalClose } = useModal();
  // zipUrl, isBusy, errorMessage,

  const [
    isBusy,
    isSuccess,
    isFailed,
    errorMessage,
    linkData,
  ] = useSelectors(
    systemSelectors.fetchDocument.getIsBusy,
    systemSelectors.fetchDocument.getIsSuccess,
    systemSelectors.fetchDocument.getIsFailed,
    systemSelectors.fetchDocument.getErrorMessage,
    systemSelectors.fetchDocument.getData,
  );

  useEffect(() => {
    if (isFailed) {
      setTimeout(() => {
        dispatch(systemActions.fetchDocument.SetData(null));
        onModalClose();
      }, 5000);
    }
  }, [isFailed]);

  useEffect(() => {
    if (data) dispatch(systemActions.fetchDocument.Call(data));
  }, [data]);

  return (
    <DataBox title="download_zip" width="30rem" separator>
      <Box p="10px" centeredContent minHeight="5rem">
        {isBusy && <Spinner size="3rem" width="5px" /> }
        {isString(linkData) && <DownloadLink target="_blank" href={linkData}>{t('download_zip_link')}</DownloadLink>}
        {isFailed && <Text mr="2rem" color="red">{errorMessage?.message}</Text>}
      </Box>
    </DataBox>
  );
};

DownloadZip.propTypes = {
  data: PropTypes.objectOf(PropTypes.any),
};

export default DownloadZip;
