import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Box from 'primitive-components/box';
import MaterialModal from '@material-ui/core/Modal';

const Scrollable = styled(Box)`
  height: 100vh;
  overflow-y: auto;
  overflow-x: none;
  outline: none;
`;

const Modal = ({ children, ...remainderProps }) => (
  <MaterialModal open {...remainderProps}>
    <Scrollable>
      <Box centeredContent minHeight="100%">
        <Box bg="#FFFFFF">
          {children}
        </Box>
      </Box>
    </Scrollable>
  </MaterialModal>
);

Modal.propTypes = {
  children: PropTypes.node.isRequired,
};

export default Modal;
