import React, { createContext, useContext } from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import { selectors, actions } from 'store/modal';
import { useI18n } from 'services/i18n';

import Button from 'components/button';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';

import Separator from 'primitive-components/separator';
import Spinner from 'primitive-components/spinner';
import Modal from './Modal';

const ModalContext = createContext(null);

const messagesTypes = {
  ERROR: 'error',
  SUCCESS: 'success',
};

const messageColors = {
  [messagesTypes.ERROR]: 'red',
  [messagesTypes.SUCCESS]: 'green',
};

const Footer = ({
  onClose, children, showSpinner, message, hideCloseButton, closeButtonLabel,
}) => {
  const t = useI18n();
  const closeButton = (
    <Button
      rounded
      m="10px"
      p="12px 30px"
      onClick={onClose}
    >
      {t(closeButtonLabel || 'close')}
    </Button>
  );

  return (
    <>
      <Separator />
      <Box>
        <Box ml="auto" display="flex" alignItems="center" width="fit-content">
          {message && (
            <Text
              m="0 1rem"
              color={messageColors[message.type]}
            >
              {t(message.text)}
            </Text>
          )}
          {showSpinner && <Spinner size="3rem" width="5px" /> }
          {children || (!hideCloseButton && closeButton)}
          {!!children && !hideCloseButton && closeButton}
        </Box>
      </Box>
    </>
  );
};

const messagePropType = PropTypes.shape({
  text: PropTypes.string,
  type: PropTypes.oneOf(Object.values(messagesTypes)),
});

Footer.propTypes = {
  onClose: PropTypes.func,
  children: PropTypes.node,
  showSpinner: PropTypes.bool,
  message: PropTypes.oneOfType([messagePropType, PropTypes.bool]),
  hideCloseButton: PropTypes.bool,
  closeButtonLabel: PropTypes.string,
};

export const ModalProvider = ({ children }) => {
  const dispatch = useDispatch();
  const components = useSelector(selectors.getComponents);

  const hasComponents = !!components.length;

  const onClose = () => dispatch(actions.PopComponentToRender());

  const renderComponent = (component, options) => (
    dispatch(actions.PushComponentToRender({ component, options }))
  );

  const ModalFooter = (props) => <Footer onClose={onClose} {...props} />;

  const value = {
    renderComponent, ModalFooter, onClose,
  };

  return (
    <ModalContext.Provider value={value}>
      {hasComponents && (
        components?.map(({ component: Component, options }, index) => {
          const { hideFooter, blockOnClose } = options || {};
          return (
            <Modal
              key={index}
              onClose={blockOnClose ? undefined : onClose}
            >
              <Component />
              {!hideFooter && <Footer onClose={onClose} />}
            </Modal>
          );
        })
      )}
      {children}
    </ModalContext.Provider>
  );
};

//
ModalProvider.propTypes = {
  children: PropTypes.node,
};

export const useModal = () => useContext(ModalContext);
