import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { useI18n } from 'services/i18n';

import Box from 'primitive-components/box';
import Text from 'primitive-components/text';
import { roles, modifiers } from 'store/contants';
import DropDownSelect from 'components/dropdown-select';
import Checkbox from 'primitive-components/checkbox';
import { remove } from 'lodash';

const valuesRoles = Object.values(roles);
const valuesModifiers = Object.values(modifiers);

const ablesRolesByModifiers = {};
valuesModifiers.forEach((mod) => {
  if (mod === modifiers.relationship) {
    ablesRolesByModifiers[mod] = [
      roles.full,
      roles.read,
      roles.remove,
      roles.write,
    ];
    return;
  }
  if (mod === modifiers.reports) {
    ablesRolesByModifiers[mod] = [
      roles.read,
      roles.full,
    ];
    return;
  }
  if (mod === modifiers.sat) {
    ablesRolesByModifiers[mod] = [
      roles.full,
      roles.update,
      roles.remove,
      roles.write,
    ];
    return;
  }
  if (mod === modifiers.entity) {
    ablesRolesByModifiers[mod] = [
      roles.update,
      roles.read,
    ];
    return;
  }
  ablesRolesByModifiers[mod] = valuesRoles;
});

const Container = styled(Box)`
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 1rem;
`;

const RolesContainer = styled(Box)`
    background-color: ${({ theme }) => theme.colors.dropdownSelectBackground};
    margin-left: 1rem;
    margin-top: 0.3rem;
    border-radius: 1rem;
    padding: 0 1rem;
`;

const ItemContainer = styled(Box)`
  margin: 0.5rem;
  display: flex;
  align-items: center;
  cursor: pointer;
`;

const stateToValue = (state) => {
  const outState = [];
  Object.keys(state).forEach((key) => {
    outState.push(...(state[key].map((val) => `${key}-${val}`)));
  });
  return outState;
};

const defaultState = {};
valuesModifiers.forEach((mod) => {
  defaultState[mod] = [];
});

const valueToState = (value) => {
  const pairsRoles = { ...defaultState };
  value.forEach((val) => {
    const [mod, role] = val.split('-');
    pairsRoles[mod] = [...pairsRoles[mod], role];
  });
  return pairsRoles;
};

const AddRoles = ({
  value, onChange, label, ...remainderProps
}) => {
  const [state, setState] = useState(defaultState);
  const [currentMod, setCurrentMod] = useState(modifiers.user);
  const t = useI18n();

  useEffect(() => {
    if (value) setState(valueToState(value));
  }, [value]);

  const isItemSelected = (valRole) => state[currentMod]?.includes(valRole);

  const applyChanges = (rolesArray) => {
    const newState = { ...state, [currentMod]: rolesArray };
    setState(newState);
    onChange?.(stateToValue(newState));
  };

  const handleSelect = (valRole) => {
    let rolesArray = state[currentMod];

    if (rolesArray.includes(valRole)) {
      remove(rolesArray, (role) => role === valRole);
      applyChanges(rolesArray);
      return;
    }

    if (valRole === roles.full) {
      rolesArray = [valRole];
      applyChanges(rolesArray);
      return;
    }

    if (rolesArray[0] === roles.full) return;

    rolesArray = [...rolesArray, valRole];
    applyChanges(rolesArray);
  };

  return (
    <Box {...remainderProps}>
      <Text ml="1rem">{t(label)}</Text>
      <Container mt="1rem">

        <Box display="flex">
          <DropDownSelect
            items={valuesModifiers}
            single
            value={currentMod}
            enableSearch
            onChange={setCurrentMod}
          />
          <RolesContainer>
            {ablesRolesByModifiers[currentMod]?.map((valRole, index) => (
              <ItemContainer key={index} onClick={() => handleSelect(valRole)}>
                <Text fontSize="sm">{valRole}</Text>
                <Checkbox
                  ml="auto"
                  backgroundColor="transparent"
                  checked={isItemSelected(valRole)}
                />
              </ItemContainer>
            ))}
          </RolesContainer>
        </Box>
      </Container>
    </Box>
  );
};

AddRoles.propTypes = {
  value: PropTypes.arrayOf(PropTypes.string),
  onChange: PropTypes.func,
  label: PropTypes.string,
};

export default AddRoles;
