import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import DropDownSelect from 'components/dropdown-select';
import DateRange from 'components/date-range';
import Date from 'components/date';
import TotalRange from 'components/total-range';
import Input from 'components/input';
import ToggleSwitch from 'components/toggle-switch';
import ToggleSwitchDouble from 'components/toggle-switch-double';

import Button from 'components/button';
import { useI18n } from 'services/i18n';

import { styledSystem } from 'services/theme/helpers';

import { ableParamsToSearchDocs } from 'store/contants';
import Box from 'primitive-components/box';
import Checkbox from 'primitive-components/checkbox';

import AddRfc from 'components/add-rfc';
import AddRoles from 'components/add-roles';

export const fieldTypes = {
  DROPDOWN: 'dropdown',
  DATE_RANGE: 'dateRange',
  DATE: 'date',
  INPUT: 'input',
  INPUT_FIELD: 'inputField',
  TOTAL_RANGE: 'totalRange',
  TOGGLE_SWITCH: 'toggleSwitch',
  TOGGLE_SWITCH_DOUBLE: 'toggleSwitchDouble',
  CHECKBOX: 'checkbox',
  ADD_RFCS: 'addRfcs',
  ADD_ROLES: 'addRoles',
};

const Container = styled(Box)`
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 1.25rem;
  width: 100%;
  ${styledSystem};
`;
// Wrappers
const WrapperDateRange = ({ value: { dateIn, dateEnd }, onChange }) => (
  <DateRange
    value={{ dateIn, dateEnd }}
    onChange={onChange}
    mb="2rem"
  />
);
WrapperDateRange.propTypes = {
  onChange: PropTypes.func,
  value: PropTypes.objectOf(PropTypes.any),
};

// //////////////////////////////////////////////////////////////////////////////////

const WrapperDate = ({ value: { date }, onChange }) => (
  <Date
    value={date}
    onChange={onChange}
    mb="2rem"
  />
);
WrapperDate.propTypes = {
  onChange: PropTypes.func,
  value: PropTypes.objectOf(PropTypes.any),
};

// //////////////////////////////////////////////////////////////////////////////////

const WrapperDropDown = ({
  fieldName, onChange, value, ...remainderProps
}) => {
  const handleOnChange = (val) => onChange({ [fieldName]: val });

  return (
    <DropDownSelect
      value={value[fieldName]}
      onChange={handleOnChange}
      {...remainderProps}
    />
  );
};

WrapperDropDown.propTypes = {
  fieldName: PropTypes.string,
  value: PropTypes.objectOf(PropTypes.any),
  onChange: PropTypes.func,
};

// //////////////////////////////////////////////////////////////////////////////////

const WrapperToggleSwitch = ({
  fieldName, onChange, value, ...remainderProps
}) => {
  const handleOnChange = (val) => onChange({ [fieldName]: val });
  return (
    <ToggleSwitch
      mb="2rem"
      state={value[fieldName]}
      onChange={handleOnChange}
      {...remainderProps}
    />
  );
};

WrapperToggleSwitch.propTypes = {
  fieldName: PropTypes.string,
  value: PropTypes.objectOf(PropTypes.any),
  onChange: PropTypes.func,
};

// //////////////////////////////////////////////////////////////////////////////////

const WrapperToggleSwitchDouble = ({
  fieldName, onChange, value, ...remainderProps
}) => {
  const handleOnChange = (val) => onChange({ [fieldName]: val });
  return (
    <ToggleSwitchDouble
      mb="2rem"
      state={value[fieldName]}
      onChange={handleOnChange}
      {...remainderProps}
    />
  );
};

WrapperToggleSwitchDouble.propTypes = {
  fieldName: PropTypes.string,
  value: PropTypes.objectOf(PropTypes.any),
  onChange: PropTypes.func,
};

// //////////////////////////////////////////////////////////////////////////////////

const WrapperCheckbox = ({
  fieldName, onChange, value, ...remainderProps
}) => {
  const handleOnChange = (val) => onChange({ [fieldName]: val });
  return (
    <Checkbox
      p="0 15px"
      m="2rem 0"
      checked={value[fieldName] || false}
      onChange={handleOnChange}
      {...remainderProps}
    />
  );
};

WrapperCheckbox.propTypes = {
  fieldName: PropTypes.string,
  value: PropTypes.objectOf(PropTypes.any),
  onChange: PropTypes.func,
};

// //////////////////////////////////////////////////////////////////////////////////

const WrapperInput = ({
  fieldName, type, errorMessage, label, onChange, value, ...remainderProps
}) => {
  const t = useI18n();

  const handleOnchange = (e) => {
    let newValue = e.target.value;

    if (type === 'number') {
      newValue = Number(newValue);
      if (!newValue) newValue = undefined;
    }
    onChange?.({ [fieldName]: newValue });
  };

  return (
    <Input
      value={value[fieldName]?.toString()}
      onChange={handleOnchange}
      label={t(label)}
      errorMessage={t(errorMessage)}
      type={type}
      mb="2rem"
      {...remainderProps}
    />
  );
};

WrapperInput.propTypes = {
  fieldName: PropTypes.string,
  value: PropTypes.objectOf(PropTypes.any),
  type: PropTypes.string,
  label: PropTypes.string,
  errorMessage: PropTypes.string,
  onChange: PropTypes.func,
};

// //////////////////////////////////////////////////////////////////////////////////

// //////////////////////////////////////////////////////////////////////////////////

const WrapperInputField = ({
  fieldName, type, errorMessage, label, ownField, onChange, value, ...remainderProps
}) => {
  const t = useI18n();

  const fieldValue = value[fieldName] || {};

  const handleOnchange = (e) => {
    let newOwnValue = e.target.value;
    if (type === 'number') {
      newOwnValue = Number(newOwnValue);
      if (!newOwnValue) newOwnValue = undefined;
    }
    const newFieldValue = { ...fieldValue, [ownField]: newOwnValue };
    onChange?.({ [fieldName]: newFieldValue });
  };

  return (
    <Input
      value={fieldValue[ownField]?.toString()}
      onChange={handleOnchange}
      label={t(label)}
      errorMessage={t(errorMessage)}
      type={type}
      mb="2rem"
      {...remainderProps}
    />
  );
};

WrapperInputField.propTypes = {
  fieldName: PropTypes.string,
  value: PropTypes.objectOf(PropTypes.any),
  type: PropTypes.string,
  label: PropTypes.string,
  errorMessage: PropTypes.string,
  onChange: PropTypes.func,
  ownField: PropTypes.string,
};

// //////////////////////////////////////////////////////////////////////////////////

const WrapperAddRfcs = ({
  fieldName, value, onChange, ...remainderProps
}) => {
  const handleOnChange = (val) => onChange?.({
    ...value,
    [fieldName]: val,
  });

  return (
    <AddRfc
      value={value[fieldName]}
      onChange={handleOnChange}
      {...remainderProps}
    />
  );
};

WrapperAddRfcs.propTypes = {
  fieldName: PropTypes.string,
  value: PropTypes.objectOf(PropTypes.any),
  onChange: PropTypes.func,
};

// //////////////////////////////////////////////////////////////////////////////////

const WrapperAddRoles = ({
  fieldName, value, onChange, ...remainderProps
}) => {
  const handleOnChange = (val) => onChange?.({
    ...value,
    [fieldName]: val,
  });

  return (
    <AddRoles
      value={value[fieldName]}
      onChange={handleOnChange}
      {...remainderProps}
    />
  );
};

WrapperAddRoles.propTypes = {
  fieldName: PropTypes.string,
  value: PropTypes.objectOf(PropTypes.any),
  onChange: PropTypes.func,
};

// //////////////////////////////////////////////////////////////////////////////////

const { TOTAL_END, TOTAL_IN } = ableParamsToSearchDocs;

const WrapperTotalRange = ({ value, onChange }) => (
  <TotalRange
    value={{
      [TOTAL_IN]: value[TOTAL_IN],
      [TOTAL_END]: value[TOTAL_END],
    }}
    onChange={onChange}
  />
);
WrapperTotalRange.propTypes = {
  onChange: PropTypes.func,
  value: PropTypes.objectOf(PropTypes.any),
};

// //////////////////////////////////////////////////////////////////////////////////

const Components = {
  [fieldTypes.DROPDOWN]: WrapperDropDown,
  [fieldTypes.DATE_RANGE]: WrapperDateRange,
  [fieldTypes.DATE]: WrapperDate,
  [fieldTypes.INPUT]: WrapperInput,
  [fieldTypes.INPUT_FIELD]: WrapperInputField,
  [fieldTypes.TOTAL_RANGE]: WrapperTotalRange,
  [fieldTypes.TOGGLE_SWITCH]: WrapperToggleSwitch,
  [fieldTypes.TOGGLE_SWITCH_DOUBLE]: WrapperToggleSwitchDouble,
  [fieldTypes.CHECKBOX]: WrapperCheckbox,
  [fieldTypes.ADD_RFCS]: WrapperAddRfcs,
  [fieldTypes.ADD_ROLES]: WrapperAddRoles,
};

// Main Component
const Form = ({
  config, value, submitButton, onChange, ...remainderProps
}) => {
  const [currentValue, setCurrentValue] = useState({});
  const t = useI18n();
  const handleChangeFilter = (change) => {
    const newValue = { ...currentValue, ...change };
    setCurrentValue(newValue);
    onChange?.(newValue);
  };

  useEffect(() => {
    setCurrentValue(value);
  }, [value]);

  const { label, onSubmit, ...submitButtonProps } = submitButton || {};

  return (
    <Container {...remainderProps}>
      {config?.map((conf, index) => {
        if (!conf) return null;

        const Component = Components[conf.fieldType];
        if (!Component) return null;

        return <Component key={index} onChange={handleChangeFilter} value={currentValue} {...conf} />;
      })}

      {!!submitButton && (
        <Button
          design="info"
          p="10px 20px"
          m="0 15px 10px 15px"
          height="fit-content"
          onClick={() => onSubmit?.(currentValue)}
          {...submitButtonProps}
        >
            {t(label)}
        </Button>

      )}

    </Container>
  );
};

Form.propTypes = {
  config: PropTypes.arrayOf(
    PropTypes.oneOfType([
      PropTypes.shape({
        fieldType: PropTypes.oneOf(Object.values(fieldTypes)),
        // custom props
      }),
      PropTypes.bool,
    ]),
  ),
  submitButton: PropTypes.shape({
    label: PropTypes.string.isRequired,
    onSubmit: PropTypes.func.isRequired,
    // custom props
  }),
  value: PropTypes.objectOf(PropTypes.any),
  onChange: PropTypes.func,
};

export default Form;
