import React from 'react';
import PropTypes from 'prop-types';
import { useI18n } from 'services/i18n';
import styled from 'styled-components';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';
import Button from 'components/button';
import Separator from 'primitive-components/separator';
import { styledSystem } from 'services/theme/helpers';

const Container = styled(Box)`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: white;
  box-shadow:${({ theme }) => theme.shadows.lg};
  ${styledSystem};
`;

const Header = styled(Box)`
  display: flex;
  align-items: center;
  padding: 1.2rem;
  cursor: default;
  user-select: none;
`;

const HeaderLabel = styled(Text)`
  color: ${({ theme }) => theme.colors.dataBoxText || '#000000'};
`;

const ContentContainer = styled(Box)`
  padding: 1rem;
  width: 100%;
  height: 100%;
`;

const DataBox = ({
  title,
  separator,
  headBtnsConfig,
  children,
  ...remainderProps
}) => {
  const t = useI18n();

  return (
    <Container {...remainderProps}>
      <Header>
        <HeaderLabel>{t(title)}</HeaderLabel>
        {!!headBtnsConfig && (
          <Box ml="auto" display="flex">
            {headBtnsConfig?.map((headerButton) => {
              if (!headerButton) return null;
              const { id, ...buttonRemainderProps } = headerButton;
              return (
                <Button key={id} p="0.5rem 1rem" m="0 5px" {...buttonRemainderProps}>
                  {t(id)}
                </Button>
              );
            })}
          </Box>
        )}
      </Header>
      {separator && <Separator />}
      <ContentContainer>{children}</ContentContainer>
    </Container>
  );
};

DataBox.propTypes = {
  title: PropTypes.string,
  separator: PropTypes.bool,
  headBtnsConfig: PropTypes.arrayOf(
    PropTypes.oneOfType([
      PropTypes.shape({
        id: PropTypes.string.isRequired,
        // custom button props
      }),
      PropTypes.bool, // omited header
    ]),
  ),
  children: PropTypes.node.isRequired,
};

export default DataBox;
