import Box from 'primitive-components/box';
import React, { useState } from 'react';
import styled, { useTheme } from 'styled-components';
import { SvgBellSolid } from 'react-line-awesome-svg';
import Icon from 'primitive-components/icon';
import { makeStyles, Popover } from '@material-ui/core';
import NotificationSrc from 'media/notification.jpg';
import Button from 'components/button';
import { useI18n } from 'services/i18n';
import NotificationItem, { NotificationType } from './NotificationItem';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    '& > *': {
      margin: theme.spacing(1),
    },
  },
  large: {
    width: '50px',
    height: '50px',
  },
  xlarge: {
    width: '80px',
    height: '80px',
    margin: '0 auto 10px',
  },
  paper: {
    width: '300px',
  },
}));

const NotificationContainer = styled(Box)`
    @keyframes swing {
        20% {
        transform: rotate3d(0, 0, 1, 15deg);
        }
    
        40% {
        transform: rotate3d(0, 0, 1, -10deg);
        }
    
        60% {
        transform: rotate3d(0, 0, 1, 5deg);
        }
    
        80% {
        transform: rotate3d(0, 0, 1, -5deg);
        }
    
        to {
        transform: rotate3d(0, 0, 1, 0deg);
        }
    }
  
    .animated {
        animation-duration: 1s;
        animation-fill-mode: both;

        &.infinite {
            animation-iteration-count: infinite;
        }
    }

    .swing {
        transform-origin: top center;
        animation-name: swing;
    }    
`;

const Badge = styled.span`
  position: absolute;
  top: 0;
  right: 10px;
  border: 3px solid #fff;
  background: #fe195e;
  display: block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  box-shadow: 0 0 0 rgba(254, 25, 94, 0.4);
  animation: BadgePulse 1.8s infinite;

  @-webkit-keyframes BadgePulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(254, 25, 94, 0.4);
  }
  70% {
    -webkit-box-shadow: 0 0 0 10px rgba(254, 25, 94, 0);
  }
  100% {
    -webkit-box-shadow: 0 0 0 20px rgba(254, 25, 94, 0);
  }
}

@keyframes BadgePulse {
  0% {
    -moz-box-shadow: 0 0 0 0 rgba(254, 25, 94, 0.4);
    box-shadow: 0 0 0 0 rgba(254, 25, 94, 0.4);
  }
  70% {
    -moz-box-shadow: 0 0 0 10px rgba(254, 25, 94, 0);
    box-shadow: 0 0 0 10px rgba(254, 25, 94, 0);
  }
  100% {
    -moz-box-shadow: 0 0 0 20px rgba(254, 25, 94, 0);
    box-shadow: 0 0 0 20px rgba(254, 25, 94, 0);
  }
}
`;

const PopoverContent = styled(Box)`

`;

const NotificationHeader = styled(Box)`
  position: relative;
`;

const NotificationTitle = styled(Box)`
  color: #fff;
  font-size: 1.2rem;
  position: absolute;
  text-align: center;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  left: 0;
  z-index: 1;
`;

const StyledImg = styled('img')`
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  border-style: none;
  border-radius: 4px 4px 0 0;
`;

const NotificationOverlay = styled(Box)`
  background: rgba(93, 83, 134, 0.8);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 4px 4px 0 0;
`;

const NotificationBody = styled(Box)`
`;

const Footer = styled(Box)`
  padding: 1.4rem 0;
  border-top: 0.07rem solid rgba(0, 0, 0, 0.05);
  background: transparent;
`;

const Notification = () => {
  const theme = useTheme();
  const [hasNotifications] = useState(true);
  const [count] = useState(3);
  const [anchorEl, setAnchorEl] = useState(null);
  const handleOpen = (event) => setAnchorEl(event.currentTarget);
  const handleClose = () => setAnchorEl(null);
  const classes = useStyles();
  const t = useI18n();
  return (
    <>
      <NotificationContainer>
        <Icon onClick={handleOpen} className={hasNotifications ? 'animated infinite swing' : ''} svg={SvgBellSolid} color={theme.colors.sideBarText} />
        {hasNotifications && <Badge /> }
      </NotificationContainer>
      <Popover
        id="notification-popover"
        anchorEl={anchorEl}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        open={Boolean(anchorEl)}
        onClose={handleClose}
        classes={{ paper: classes.paper }}
      >
        <PopoverContent>
          <NotificationHeader>
            <NotificationTitle>
              {t('notification_title', { count })}
            </NotificationTitle>
            <NotificationOverlay />
            <StyledImg src={NotificationSrc} alt="Notification" />
          </NotificationHeader>
          <NotificationBody>
            <NotificationItem title="Nuevo usuario creado" date={new Date().toDateString()} notificationType={NotificationType.NEW_USER} />
            <NotificationItem title="Nuevo proveedor añadido" date={new Date().toDateString()} notificationType={NotificationType.NEW_PROVIDER} />
          </NotificationBody>
          <Footer centeredContent>
            <Button design="link">{t('view_all_notification')}</Button>
          </Footer>
        </PopoverContent>
      </Popover>
    </>
  );
};

export default Notification;
