import React from 'react';
import { useI18n } from 'services/i18n';
import PropTypes from 'prop-types';
import Text from 'primitive-components/text';
import styled from 'styled-components';
import { pageSize } from 'store/contants';

const StyledDetails = styled(Text)`
  color: ${({ theme }) => theme.colors.placeholderText};
  padding: 7px;
`;

const Details = ({ pagination, ...remainderProps }) => {
  const t = useI18n();
  const {
    page,
    totalDocs,
  } = pagination;

  const beginDocs = (((page - 1) * pageSize) + 1);
  const end = (((page - 1) * pageSize) + pageSize);
  const endDocs = end > totalDocs ? totalDocs : end;

  return (
    <StyledDetails fontSize="sm" {...remainderProps}>
      {t('pagination_details', {
        beginDocs,
        endDocs,
        totalDocs,
      })}
    </StyledDetails>
  );
};

Details.propTypes = {
  pagination: PropTypes.shape({
    page: PropTypes.number,
    totalPages: PropTypes.number,
    totalDocs: PropTypes.number,
  }).isRequired,
};

export default Details;
