import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import useSelectors from 'helpers/useSelectors';
import {
  actions as docsActions,
  selectors as docsSelectors,
} from 'store/documents';
import { useHasRoles } from 'services/hasRoles';
import { selectors as userSelectors } from 'store/user';
import {
  purchaseOrderStates,
  paramsToFetchPurchaseOrders,
  ableParamsToFetch,
} from 'store/contants';
import useSearch from 'services/search/useSearch';
import { useModal } from 'services/modal';
import {
  SvgPlusSolid,
  // SvgDownloadSolid,
  SvgSyncSolid,
} from 'react-line-awesome-svg';
import Accordion from 'primitive-components/accordion';
import Page from 'components/page';
import DataBox from 'components/data-box';
import Form, { fieldTypes } from 'components/form';
import TableBox, { tableTypes } from 'components/table-box';
import NewPurchaseOrder from 'scenes/modals/new-purchase-order';

const valuesPurchaseOrderStates = Object.values(purchaseOrderStates);

const PurchaseOrders = () => {
  const dispatch = useDispatch();
  const { search, params, updateParams } = useSearch();

  const { renderComponent } = useModal();
  const [
    rfcsIssuer,
    rfcsReceiver,
    purchaseOrders,
    isBusy,
    pagination,

    isClient,
  ] = useSelectors(
    userSelectors.fetchRFCs.getRFCsIssuer,
    userSelectors.fetchRFCs.getRFCsReceiver,
    docsSelectors.getDocs1,
    docsSelectors.getDocs1IsBusy,
    docsSelectors.getDocs1Pagination,

    userSelectors.fetchUser.getIsClient,
  );

  const tableType = isClient ? tableTypes.USER_PURCHASE_ORDERS : tableTypes.PROVIDER_PURCHASE_ORDERS;

  const FetchDocs = () => dispatch(docsActions.FetchDocs1({
    ...params,
    ...paramsToFetchPurchaseOrders,
  }));

  const handleChangePage = (pageT) => updateParams({ page: pageT });
  const handleOpenNewPurchase = () => renderComponent(NewPurchaseOrder, { hideFooter: true });

  useEffect(() => {
    FetchDocs();
  }, [search]);

  const formConfig = [
    {
      fieldType: fieldTypes.TOTAL_RANGE,
    },
    {
      label: 'document_number',
      fieldType: fieldTypes.INPUT,
      type: 'number',
      fieldName: ableParamsToFetch.PURCHASE_ORDER,
    },
    {
      fieldType: fieldTypes.DATE_RANGE,
    },
    {
      label: 'issuing_rfc',
      enableSearch: true,
      items: rfcsIssuer,
      fieldType: fieldTypes.DROPDOWN,
      fieldName: ableParamsToFetch.RFC_ISSUER,
    },
    {
      label: 'receiver_rfc',
      enableSearch: true,
      items: rfcsReceiver,
      fieldType: fieldTypes.DROPDOWN,
      fieldName: ableParamsToFetch.RFC_RECEIVER,
    },
    {
      label: 'state',
      enableSearch: true,
      items: valuesPurchaseOrderStates,
      fieldType: fieldTypes.DROPDOWN,
      fieldName: ableParamsToFetch.STATE,
    },
  ];

  const { document: { full, write } } = useHasRoles();
  const headBtnsConfig = [
    isClient && (full || write) && {
      id: 'new', design: 'primary', svgIcon: SvgPlusSolid, onClick: handleOpenNewPurchase,
    },
    // { id: 'export', design: 'info', svgIcon: SvgDownloadSolid },
    {
      id: 'update', design: 'shadow', svgIcon: SvgSyncSolid, rounded: true, onClick: FetchDocs,
    },
  ];

  return (
    <Page title="purchase_orders" rfcsRequired>
      <DataBox
        title="purchase_order"
        headBtnsConfig={headBtnsConfig}
        separator
      >
        <Accordion title="filter_search">
          <Form
            config={formConfig}
            value={params}
            submitButton={{
              label: 'apply',
              onSubmit: updateParams,
            }}
          />
        </Accordion>

        <TableBox
          tableType={tableType}
          elements={purchaseOrders}
          noFoundMessage="no_purchase_orders_found"
          isBusy={isBusy}
          onChangePage={handleChangePage}
          pagination={pagination}
        />
      </DataBox>
    </Page>

  );
};

export default PurchaseOrders;
