import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { useI18n } from 'services/i18n';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';
import Checkbox from 'primitive-components/checkbox';
import Input from 'components/input';
import styled from 'styled-components';
import { styledSystem } from 'services/theme/helpers';
import { some } from 'lodash';
import {
  ClickAwayListener, Grow, Paper, Popper,
} from '@material-ui/core';

const Container = styled(Box)`
  margin-bottom: 2rem;
  padding: 0px 15px;
  width: 225px;
  position: relative;
  ${styledSystem};
`;

const Label = styled(Text)`
  white-space: nowrap;
`;

const InputContainer = styled(Box)`
  margin-top: 10px;
  background-color: ${({ theme }) => theme.colors.dropdownSelectBackground};
  border-radius: 5rem;
  padding: 10px 20px;
  cursor: pointer;
  color: ${({ theme, hasSelectedItems }) => theme.colors[
    hasSelectedItems ? 'dropdownSelectTextSelected' : 'dropdownSelectText'
  ]};
`;

const InputText = styled(Text)`
  min-width: 7rem;
  white-space: nowrap;
`;

const DownTriangle = styled(Text)`
  &::after {
    content: "▾";
  }
`;

const ItemContainer = styled(Box)`
  margin: 0.5rem;
  display: flex;
  align-items: center;
  cursor: pointer;
`;

const Scrollable = styled(Box)`
  overflow-y: auto;
  max-height: 12rem;
`;

const defaultSelectedItems = [];

const DropDownSelect = ({
  label,
  enableSearch,
  items = [],
  onChange,
  value,
  optionValueField,
  optionTextField,
  single,
  ...remainderProps
}) => {
  const [anchorEl, setAnchorEl] = useState(null);
  const [selectedItems, setSelectedItems] = useState(defaultSelectedItems);
  const [searchText, setSearchText] = useState('');
  const t = useI18n();

  if (optionValueField && !optionTextField) throw new Error('You should to define <optionTextField> prop');
  if (items?.length && (typeof items[0] === 'object') && !optionTextField) throw new Error('You should to define <optionTextField> prop');

  const handleOpen = (event) => setAnchorEl(event.currentTarget);
  const handleClose = () => setAnchorEl(null);

  const getValueField = (item) => item[optionValueField] || item;
  const getTextField = (item) => item[optionTextField] || item;

  const getItemFromValue = (val) => (optionValueField ? items?.find((item) => getValueField(item) === val) : val);
  const getItemsFromValues = (values) => (optionValueField ? values?.map((val) => getItemFromValue(val)) : values);

  const areSameItems = (item1, item2) => getTextField(item1) === getTextField(item2);

  const isItemSelected = (item) => some(
    selectedItems, (selectedItem) => areSameItems(selectedItem, item),
  );

  const handleSelect = (select) => {
    if (single) {
      setSelectedItems([getValueField(select)]);
      onChange?.(getValueField(select));

      handleClose();
      return;
    }

    const isSelected = isItemSelected(select);
    const updatedSelectedItems = isSelected
      ? selectedItems.filter((item) => !areSameItems(item, select))
      : [...selectedItems, select];

    setSelectedItems(updatedSelectedItems);
    onChange?.(updatedSelectedItems?.map((item) => getValueField(item)));
  };

  const getInputText = () => {
    const { length } = selectedItems;
    if (length === 0) return t('nothing_selected');
    return length === 1 ? getTextField(selectedItems[0]) : t('selected_items', { length });
  };

  const matchWithSearch = (text) => text?.toLowerCase().includes(searchText?.toLowerCase());

  useEffect(() => {
    if (anchorEl) setSearchText('');
  }, [anchorEl]);

  useEffect(() => {
    if (!items?.length) return;
    if (single) {
      const itemT = getItemFromValue(value);
      setSelectedItems(itemT ? [itemT] : defaultSelectedItems);
    } else {
      const itemsT = getItemsFromValues(value);
      setSelectedItems(itemsT || defaultSelectedItems);
    }
  }, [value, single, items]);

  return (
    <Container {...remainderProps}>
      <Label fontSize="sm" mb="0.5rem">
        {t(label)}
      </Label>
      <InputContainer
        centeredContent
        fontSize="xs"
        fontWeight="bld"
        onClick={handleOpen}
        hasSelectedItems={!!selectedItems.length}
      >
        <InputText>{getInputText()}</InputText>
        <DownTriangle />
      </InputContainer>

      {!!items?.length && (
      <Popper
        open={Boolean(anchorEl)}
        anchorEl={anchorEl}
        role={undefined}
        transition
        disablePortal
        style={{ zIndex: '999' }}
      >
        {({ TransitionProps, placement }) => (
          <Grow
            {...TransitionProps}
            style={{ transformOrigin: placement === 'bottom' ? 'center top' : 'center bottom' }}
          >
            <Paper>
              <ClickAwayListener onClickAway={handleClose}>
                <Scrollable>
                  {enableSearch && (
                  <Input
                    value={searchText}
                    onChange={(e) => setSearchText(e.target.value)}
                  />
                  )}
                  <Box ml="1rem">
                    {items?.map((item, index) => {
                      const textField = getTextField(item);
                      if (!matchWithSearch(textField)) return null;

                      return (
                        <ItemContainer key={index} onClick={() => handleSelect(item)}>
                          <Text fontSize="sm">{textField}</Text>
                          <Checkbox
                            ml="auto"
                            backgroundColor="transparent"
                            checked={isItemSelected(item)}
                          />
                        </ItemContainer>
                      );
                    })}
                  </Box>
                </Scrollable>
              </ClickAwayListener>
            </Paper>
          </Grow>
        )}
      </Popper>
      )}
    </Container>
  );
};

DropDownSelect.propTypes = {
  /**
   * Listado de elementos que serán mostrado en el dropdown
   */
  items: PropTypes.arrayOf(PropTypes.any),
  /**
   * Representa el valor que será retornado
   * para cada item seleccionado en el dropdown
   * Utilizarlo cuando se trabaja con objetos dentro del dropdown
   */
  optionValueField: PropTypes.string,
  /**
   * Representa el texto que será mostrado
   * para cada item en el dropdown
   * Utilizarlo cuando se trabaja con objetos dentro del dropdown
   */
  optionTextField: PropTypes.string,
  /**
   * Función que se llama cuando el valor de dropdown cambia
   */
  onChange: PropTypes.func,
  /**
   * Representa el o los elementos seleccionados en el dropdown
   * al inicializar el componente.
   */
  value: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.any),
    PropTypes.any, // used with single
  ]),
  /**
   * Etiqueta para denominar el nombre del campo
   */
  label: PropTypes.string,
  /**
   * Habilita la búsqueda en el dropdown
   */
  enableSearch: PropTypes.bool,
  /**
   * Permite selección simple en el dropdown
   */
  single: PropTypes.bool,
};

export default DropDownSelect;
