import React from 'react';
import PropTypes from 'prop-types';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';

export const heads = [
  'areas',
  'rfcs',
  'company_name',
];

const List = ({ data }) => (
  <Box display="flex" flexDirection="column">
    {data?.map((d, index) => (<Text key={index} mt="5px">{d}</Text>))}
  </Box>
);
List.propTypes = {
  data: PropTypes.arrayOf(PropTypes.string),
};

export const transmute = (data) => {
  const {
    areaName, areaRfcs,
  } = data;

  return [
    areaName,
    <List data={areaRfcs?.map(({ code }) => code)} />,
    <List data={areaRfcs?.map(({ company }) => company)} />,
  ];
};
