import Box from 'primitive-components/box';
import Form from 'primitive-components/form';
import Icon from 'primitive-components/icon';
import Input from 'primitive-components/input';
import React, { useEffect, useState } from 'react';
import { SvgSearchSolid, SvgTimesSolid } from 'react-line-awesome-svg';
import styled, { useTheme } from 'styled-components';

const SearchBox = styled(Box)`
    width: 100%;
    height: 80px;
    position: fixed;
    top: ${({ showSearchBar }) => (showSearchBar ? '0px' : '-80px')};
    right: 0;
    padding: 0;
    background: #fff;
    z-index: 9999;
    border-radius: 0;
    transition: 0.3s all ease-in-out;
`;

const DismissButton = styled.button`
    position: absolute;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
    background: none;
    border: none;
    cursor: pointer;
    font-size: 1.2rem;
    color: #5d5386;
    outline:none;
`;

const SearchForm = styled(Form)`
height: 100%;
`;

const SearchInput = styled(Input)`
    height: 100%;
    border: none;
    padding: 20px;
    color: #5d5386;
    font-size: 1rem;
    line-height: 1.25;
    display: block;
    width: 100%;
    font-weight: 400;
    background-color: #fff;
    background-clip: padding-box;
    outline:none;
    outline-offset: -2px;
    -webkit-appearance: none;
    &::-webkit-search-cancel-button{
        display:none;   
    }
`;

const Search = () => {
  const theme = useTheme();
  const [showSearchBar, setShowSearchBar] = useState(false);
  const [text, setText] = useState('');

  const handleShowSearchBar = () => {
    setShowSearchBar(!showSearchBar);
  };

  const handleTextChange = (event) => {
    setText(event.target.value);
  };

  useEffect(() => {
    if (!showSearchBar) {
      setText('');
    }
  }, [showSearchBar]);

  return (
    <>
      <Icon svg={SvgSearchSolid} color={theme.colors.sideBarText} onClick={handleShowSearchBar} />
      <SearchBox showSearchBar={showSearchBar}>
        <DismissButton>
          <Icon svg={SvgTimesSolid} color={theme.colors.sideBarText} onClick={handleShowSearchBar} />
        </DismissButton>
        <SearchForm>
          <SearchInput type="search" value={text} onChange={handleTextChange} placeholder="Buscar... Ejemplo facturas recibidas" />
        </SearchForm>
      </SearchBox>
    </>
  );
};

export default Search;
