import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { useI18n } from 'services/i18n';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';

import Input from 'components/input';
import PInput from 'primitive-components/input';

import styled from 'styled-components';
import Popover from '@material-ui/core/Popover';
import { ableParamsToSearchDocs } from 'store/contants';
import { styledSystem } from 'services/theme/helpers';

const StyledInput = styled(Input)`
  width: 8rem;
  margin-top: 5px;
`;

const { TOTAL_IN, TOTAL_END } = ableParamsToSearchDocs;

const toTotalRange = (value) => {
  let newValue = {};

  if (value[TOTAL_IN]) newValue = { ...newValue, [TOTAL_IN]: value[TOTAL_IN] };
  if (value[TOTAL_END]) newValue = { ...newValue, [TOTAL_END]: value[TOTAL_END] };

  return newValue;
};

const defaultValue = {};

const StyledPInput = styled(PInput)`
  text-align: center;
  cursor: default;
  border: none;
  background-color: transparent;
  padding: 0.5rem;
  color: ${({ theme }) => theme.colors.inputText || '#000000'};
  line-height: 1.25;
  width: 5rem;
  &:focus {
      outline: none;
   }
`;

const Container = styled(Box)`
    display: flex;
    flex-direction: column;
    padding: 0 15px;
    margin-bottom: 2rem;

    ${styledSystem};
`;

const InputContainer = styled(Box)`
  display: flex;
  align-items: center;
  border-radius: 4px;
  border: solid 1px; 
  border-color: ${({ theme, isValid }) => (
    isValid ? theme.colors.inputBorder || '#000000' : 'red'
  )};
`;

const TotalRange = ({
  onChange,
  value,
  ...remainderProps
}) => {
  const [anchorEl, setAnchorEl] = useState(null);
  const [currentValue, setCurrentValue] = useState(defaultValue);
  const [isValid, setIsValid] = useState(true);

  const t = useI18n();

  const handleOpen = (event) => setAnchorEl(event.currentTarget);
  const handleClose = () => setAnchorEl(null);

  const handleIsValid = (min, max) => {
    if (!min || !max) return true;
    if (min >= max) return false;
    return true;
  };

  const handleOnChange = (name, val) => {
    const min = currentValue[TOTAL_IN];
    const max = currentValue[TOTAL_END];

    const newVal = Number(val);

    const valid = handleIsValid(
      name === TOTAL_IN ? newVal : min,
      name === TOTAL_END ? newVal : max,
    );

    const change = {
      ...currentValue,
      ...{ [name]: newVal || undefined },
    };
    setIsValid(valid);
    setCurrentValue(change);

    setIsValid(valid);
    setCurrentValue(change);
    onChange?.(change);
  };

  useEffect(() => {
    setCurrentValue(toTotalRange(value));
  }, [value]);

  return (
    <>
      <Container {...remainderProps}>
        <Text mb="7px" fontSize="sm">{t('total')}</Text>
        <InputContainer onClick={handleOpen} isValid={isValid}>
          <StyledPInput
            readOnly
            value={`${currentValue[TOTAL_IN] || 'min'}`}
          />
          <Text m="5px">-</Text>
          <StyledPInput
            readOnly
            value={`${currentValue[TOTAL_END] || 'max'}`}
          />
        </InputContainer>
      </Container>
      <Popover
        anchorEl={anchorEl}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <Box display="flex">
          <StyledInput
            placeholder="min"
            min="0"
            type="number"
            value={(currentValue[TOTAL_IN] || '').toString()}
            onChange={(e) => handleOnChange(TOTAL_IN, e.target.value)}
          />

          <StyledInput
            placeholder="max"
            min="0"
            type="number"
            value={(currentValue[TOTAL_END] || '').toString()}
            onChange={(e) => handleOnChange(TOTAL_END, e.target.value)}
          />
        </Box>
      </Popover>
    </>
  );
};

TotalRange.propTypes = {
  onChange: PropTypes.func,
  value: PropTypes.shape({
    [TOTAL_IN]: PropTypes.number,
    [TOTAL_END]: PropTypes.number,
  }),
};

export default TotalRange;
